import { Component, OnInit, Input } from "@angular/core";
import { Router } from "@angular/router";

@Component({
    selector: "app-form-list-cpnt",
    templateUrl: "./form-list-cpnt.component.html",
    styleUrls: ["./form-list-cpnt.component.scss"]
})
export class FormListCpntComponent implements OnInit {
    @Input() table: { head: string[]; body: any[] };

    constructor(private router: Router) {}

    ngOnInit() {}

    goToCommande(id) {
        this.router.navigate(["shop", "commandes", "commande", id]);
    }
}



