  reduceArr(iterable) {
    let arr = [];

    if (Array.isArray(iterable)) {
      iterable.forEach(element => {
        arr.push(element.id);
      });
    } else {
      // console.log("Not iterable :", iterable);
    }

    return arr;
  }

  fillDropdowns() {
    if (!this.isAE) {
      if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
        this.fillDropdownData(
          <FormArray>this.produitForm.controls["groupeRegleTaxe"],
          [this.produit["groupeRegleTaxe"].id],
          true
        );
      }
    }

    if (typeof this.produit["fabricant"] != "undefined") {
      this.fillDropdownData(
        <FormArray>this.produitForm.controls["fabricant"],
        [this.produit["fabricant"].id],
        true
      );
    }

    this.fillDropdownData(
      <FormArray>this.produitForm.controls["categories"],
      this.reduceArr(this.produit["categories"]),
      false
    );
    this.fillDropdownData(
      <FormArray>this.produitForm.controls["fournisseurs"],
      this.reduceArr(this.produit["fournisseurs"]),
      false
    );
    this.fillDropdownData(
      <FormArray>this.produitForm.controls["tags"],
      this.reduceArr(this.produit["tags"]),
      false
    );

    //console.log(this.produit);

    let msg = this.produit["messageDisponibiliteProduits"];

    if (msg.length > 0) {
      let messageS = msg.filter(msg => msg.reference == 0);
      let messageHs = msg.filter(msg => msg.reference == 1);
      let messageSc = msg.filter(msg => msg.reference == 2);

      if (messageS.length > 0) {
        this.fillDropdownData(
          <FormArray>this.produitForm.controls["messagestocks"],
          [messageS[0].id],
          false
        );
      }

      if (messageHs.length > 0) {
        this.fillDropdownData(
          <FormArray>this.produitForm.controls["messagehorsstocks"],
          [messageHs[0].id],
          false
        );
      }

      if (messageSc.length > 0) {
        this.fillDropdownData(
          <FormArray>(
            this.produitForm.controls["messagestockcritiques"]
          ),
          [messageSc[0].id],
          false
        );
      }
    }

    if (this.hasDelivery) {
      this.fillDropdownData(
        <FormArray>this.produitForm.controls["transporteurs"],
        this.reduceArr(this.produit["transporteurs"]),
        false
      );
    }
  }

