  /**
   * 
   * @param v Formatte la data du dropdown avant submission du POST
   * @param toReduce 
   * @param firstOnly 
   */
  formatDropdownPost(v, toReduce, firstOnly) {
    if (typeof v[toReduce] == "undefined") {
      //console.log(toReduce, "not existing ");
      return v;
    }
    let tempClone = [];

    //console.log("formatBeforePost :", v, toReduce );
    for (let i = 0; i < v[toReduce].length; i++) {
      tempClone.push(v[toReduce][i].id);
    }
    if (firstOnly) {
      v[toReduce] = tempClone[0];
    } else {
      v[toReduce] = tempClone;
    }

    //console.log("FormatBeforePost Return:", v);
    return v;
  }

  formatDropdownMessage(v, toReduce) {
    let tempClone = [];
    if (typeof v[toReduce] != "undefined") {
      for (let i = 0; i < v[toReduce].length; i++) {
        if (v[toReduce][i].id != 0) {
          tempClone.push(v[toReduce][i].id);
        }
      }

      delete v[toReduce];
      if (!isNaN(tempClone[0])) {
        v["messageDisponibiliteProduits"].push(tempClone[0]);
      }
    } else {
      // console.log(toReduce, "not found :", v[toReduce]);
    }

    return v;
  }

  /**
   * Activation du produit
   */
  onActivate() {

    this.isLoading = true;
    //on est obligé de publier systématiquement la page à cause du online/offline du dash
    this.getLinkedPage().then(e => {
      if (!this.produitForm.get("active").value) {
        this.siteApi.apiPagesPagePublishPut(String(this.metaPage.page.id)).subscribe(e => {
          console.log("page has no pending updates...");
          this.produitApi
            .apiMagasinsSlugProduitsActivesMultisPost(
              [{ id: this.Id }],
              this.store.shopId.toString()
            )
            .subscribe(e => {
              this.isLoading = false;
              this.produitForm.patchValue({
                active: !this.produitForm.get("active").value
              });
            });
        });
      } else {

        this.produitApi
          .apiMagasinsSlugProduitsActivesMultisPost(
            [{ id: this.Id }],
            this.store.shopId.toString()
          )
          .subscribe(e => {
            this.isLoading = false;
            this.produitForm.patchValue({
              active: !this.produitForm.get("active").value
            });
          });

      }

    });



  }


  // Crée une page formation 
  createPage(resolve, reject) {

    const page: PageType = {
      nom: this.produit['titre'],
      url: "formation/" + this.produit.slug,
      editor: "10", // choper lediteur sur le site
      is_home: false,
      is_error: false,
      module: "3",
      module_resource: this.produit.id,
      module_page_type: "formation",
      is_service: false
    };

    if (this.metadata) {
      page['metadata'] = this.metadata;
    }

    this.siteApi
      .apiSitesSitePagesPost(page, String(this.store.curSite.id))
      .subscribe(p => {
        //console.log("creating page...", p);
        this.metaPage.page = p;
        resolve(p);
      }, err => {
        reject();
      })
  }


  /**
   * Recupere la page associée ou en créé une
   */
  getLinkedPage() {

    return new Promise((resolve, reject) => {

      if (this.metaPage.page) {
        resolve(this.metaPage.page);
      } else {
        this.moduleApi
          .apiSitesSlugModulesModulePagesModuleResourceGet(
            String(this.store.curSite.id),
            "3",
            this.produit.id
          )
          .subscribe(
            page => {
              this.metaPage.page = page;
              resolve(page);
            }, err => {
              return this.createPage(resolve, reject);
            });
      }

    });

  }



  onPublishPage() {
    this.isLoading = true;
    this.getLinkedPage().then(e => {
      this.siteApi.apiPagesPagePublishPut(String(this.metaPage.page.id)).subscribe(e => {
        console.log("page has no pending updates...");
        if (!this.produitForm.get("active").value) {
          this.produitApi
            .apiMagasinsSlugProduitsActivesMultisPost(
              [{ id: this.Id }],
              this.store.shopId.toString()
            )
            .subscribe(e => {
              this.isLoading = false;
              this.produitForm.patchValue({
                active: !this.produitForm.get("active").value
              });
            });
        } else {
          this.isLoading = false;
        }

      });
    });


  }

