  getFormValidationErrors(f) {
    this.notifService.reset();

    Object.keys(f.controls).forEach(key => {
      if (!f.get(key).valid) {
        // console.log(f.get(key), f.get(key).valid);
      }

      const controlErrors: ValidationErrors = f.get(key).errors;
      if (controlErrors != null) {
        Object.keys(controlErrors).forEach(keyError => {
          /*           console.log("TODO messages des erreurs");
                    console.log("keyError", keyError);
                    console.log(
                      "Key control: " +
                      key +
                      ", keyError: " +
                      keyError +
                      ", err value: ",
                      controlErrors[keyError]
                    ); */
          if (key == "downloadLink") {
            this.notifService.notify(`Vous devez sélectionner un fichier téléchargeable`, null, "danger");
          }
          // this.notifService.notify(`${key} : ${this.getMsgValidation(controlErrors[keyError])}`, null, "danger");
        });
      }
    });
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

