  onDropdownDeleted($event) {
    //console.log('deleted', $event);
    let module = this.modules.filter(
      module => module.name == $event.module
    );
    let formArray = <FormArray>(
      this.produitForm.controls[module[0]["props"]]
    );
    let valuesToKeep = [];

    for (let i = 0; i < formArray.value.length; i++) {
      if (formArray.value[i].id == $event.id.id) {
        formArray.removeAt(i);
      } else {
        //console.log(formArray.value[i].id, "!=", $event.id.id);
        valuesToKeep.push(formArray.value[i]);
      }
    }
    this.deleteBdd(module[0].name, $event.id.id);

    //console.log("valuesToKeep", valuesToKeep);
    this.dropdownPlaceholderText(module[0], valuesToKeep);
  }

  onDropdownSelected($event) {
    //console.log("onDropdownSelected :", $event);

    let module = this.modules.filter(
      module => module.name == $event.module
    );
    let formArray = <FormArray>(
      this.produitForm.controls[module[0]["props"]]
    );
    this.clearFormArray(formArray);

    if ($event.ids.length == 0) {
      //formArray.setValue([{id:""}]);
    } else {
      $event.ids.forEach(element => {
        if (module["required"]) {
          formArray.push(
            this.fb.group(
              { id: [element, Validators.required] },
              Validators.required
            )
          );
        } else {
          formArray.push(
            this.fb.group({ id: [element, Validators.required] })
          );
        }
      });
    }

    if ("tva" == module[0].name) {
      this.produit["groupeRegleTaxe"] = $event.items[0];
      this.finalPrice(this.produit["prixHt"]);
    }

    let dropName = module[0]["dropdown"];
    this.dropdownPlaceholderText(module[0], $event.items);
    this[dropName] = false;
  }

