  buildExistingVariants(declinaisons) {
    let existingCombi = [];

    if (declinaisons.length > 0) {
      const control = <FormArray>(
        this.produitForm.controls["declinaisons"]
      );
      control.removeAt(0);
    }

    for (let e = 0; e < declinaisons.length; e++) {
      this.pushVariantOnForm(declinaisons[e]);
      let attr = new Array(declinaisons[e].attributValeurs.length);
      for (let i = 0; i < declinaisons[e].attributValeurs.length; i++) {
        attr[i] = declinaisons[e].attributValeurs[i].id;
      }

      existingCombi.push(attr);
    }

    return existingCombi;
  }

