  onVariantDeleted($event, formIndex) {
    //console.log($event, "decli à del bdd");

    this.produitApi
      .apiMagasinsSlugProduitsProduitDelete(
        this.store.shopId.toString(),
        $event.id
      )
      .subscribe(e => {
        let attr = new Array($event.attributValeurs.length);
        for (let i = 0; i < $event.attributValeurs.length; i++) {
          attr[i] = $event.attributValeurs[i].id;
        }

        for (let z = 0; z < this.existingVariations.length; z++) {
          if (this.arraysEqual(attr, this.existingVariations[z])) {
            this.existingVariations.splice(z, 1);
          }
        }

        let index = this.declinaisons.indexOf($event);
        if (index > -1) {
          this.declinaisons.splice(index, 1);
        }

        const control = <FormArray>(
          this.produitForm.controls["declinaisons"]
        );
        control.removeAt(formIndex);
      });
  }

  onVariantAdded($event) {
    let attributsIter = [...$event.attributs];
    let attributs = [];

    for (let b = 0; b < attributsIter.length; b++) {
      attributsIter[b] = attributsIter[b].attributValeurs.filter(
        av => av.id == $event.combinaison[b]
      )[0];
      attributs.push(attributsIter[b]);
    }

    //console.log("attributs parsés : ", attributs);

    let variantPlaceholder = {
      active: true,
      reference: "",
      conditionnement: 0,
      titre: "SPECIMEN",
      imageUrl: null,
      prixHt: this.produit["prixHt"].toString().replace(".", ","),
      variationHt: 0,
      specialPriceTtc: Number(
        this.finalPriceValue.toString().replace(",", ".")
      ),
      quantite: this.produit["quantite"],
      stockCritique: this.produit["stockCritique"],
      poids: this.produit["poids"],
      dateDisponibilite: this.produitForm.get("dateDisponibilite").value,
      //"prixSpecifics": [],
      attributValeurs: attributs
    };

    let placehold = { ...variantPlaceholder };
    placehold["parent"] = this.Id;
    placehold["isVariation"] = true;

    let attributsValClone = [];
    for (let b = 0; b < attributs.length; b++) {
      attributsValClone.push(attributs[b].id);
    }
    placehold.attributValeurs = attributsValClone;

    let theDate = placehold["dateDisponibilite"].split("/");
    // console.log(
    //     ' placehold["dateDisponibilite"]',
    //     placehold["dateDisponibilite"]
    // );
    placehold["dateDisponibilite"] = {
      date: {
        // tslint:disable-next-line:radix
        year: parseInt(theDate[2]).toString(),
        month: parseInt(theDate[1]).toString(),
        day: parseInt(theDate[0]).toString()
      },
      time: {
        hour: "0",
        minute: "0"
      }
    };

    // formulePaymentReccurent: number;
    // formulePaymentFreq: number;
    // formuleDuration: number;
    // formuleGroupe: string;

    placehold["specialPriceTtc"] = placehold.specialPriceTtc;
    placehold["formulePaymentReccurent"] = 1;
    placehold["formulePaymentFreq"] = 1;
    placehold["formuleDuration"] = 1;
    placehold["formuleGroupe"] = "[]";

    this.produitApi
      .apiMagasinsSlugProduitsPost(
        placehold,
        this.store.shopId.toString()
      )
      .subscribe(e => {
        //console.log("Declinaison In BDD :", e);
        this.pushVariantOnForm(e);
        this.declinaisons.push(e);
      });
  }

  convertTtcToHt(e) {
    let prix = e.toString().replace(",", ".");

    let txTva = 1;
    if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
      txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
    }

    return (parseFloat(prix) / txTva).toFixed(6).replace(".", ",");
  }

  modVariantTtc($event, e) {
    let txTva = 1;
    if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
      txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
    }
    let variationHt = this.convertTtcToHt($event.target.value);
    let prixHt = (
      parseFloat(this.produit["prixHt"].toString().replace(",", ".")) +
      parseFloat(variationHt.toString().replace(",", "."))
    ).toFixed(6);
    let prixTtc = (parseFloat(prixHt) * txTva).toFixed(2);

    let promo = prixTtc;

    if (this.produit["prixSpecifics"].length > 0) {
      if (!this.produit["prixSpecifics"][0].typePromotion) {
        promo = (
          parseFloat(prixHt) *
          txTva *
          (1 -
            parseFloat(
              this.produit[
                "prixSpecifics"
              ][0].promotion.toString()
            ) /
            100)
        )
          .toFixed(2)
          .toString()
          .replace(".", ",");
      } else {
        promo = (
          txTva * parseFloat(prixHt) -
          parseFloat(this.produit["prixSpecifics"][0].promotion)
        )
          .toFixed(2)
          .toString()
          .replace(".", ",");
      }

      if (parseFloat(promo.replace(",", ".")) < 0) {
        promo = "0";
      }
    }

    /*     console.log(
           "modification :", {
           "prixProduit :" : (parseFloat(this.produit['prixHt'].toString().replace(",", '.'))*txTva).toFixed(2) ,
          'variationTtc': $event.target.value,
          'variationHt': variationHt,
          'prixHt': prixHt,
          'prixTtc' : prixTtc,
          'promo': promo}); */

    if (e) {
      e.patchValue({
        variationHt: variationHt,
        prixHt: prixHt,
        prixTtc: prixTtc,
        specialPriceTtc: promo
      });
    } else {
      return {
        variationHt: variationHt,
        prixHt: prixHt,
        prixTtc: prixTtc,
        specialPriceTtc: promo
      };
    }
  }

  pushVariantOnForm(variantPlaceholder) {
    const control = <FormArray>this.produitForm.controls["declinaisons"];

    let prix = variantPlaceholder.prixHt.toString().replace(".", ",");
    let variationHt = variantPlaceholder.variationHt
      .toString()
      .replace(".", ",");

    let txTva = 1;
    if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
      txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
    }

    // console.log("prixHt utilisé :", prix);
    // console.log("txTva utilisé :", txTva);

    let prixTtc = (parseFloat(prix.toString().replace(",", ".")) * txTva)
      .toFixed(2)
      .toString()
      .replace(".", ",");
    let variationTtc = (
      parseFloat(variationHt.toString().replace(",", ".")) * txTva
    )
      .toFixed(2)
      .toString()
      .replace(".", ",");
    // add new formgroup
    let newDeclinaison = this.fb.group({
      ...variantPlaceholder,
      selected: [false, Validators.required],
      reference: [
        variantPlaceholder.reference,
        [
          Validators.required,
          Validators.minLength(2),
          Validators.maxLength(50)
        ]
      ],
      prixHt: [
        prix,
        [
          Validators.required,
          Validators.min(0),
          Validators.pattern(this.pricePattern)
        ]
      ],
      variationTtc: [
        variationTtc,
        [Validators.required, Validators.pattern(this.variationPattern)]
      ],
      variationHt: [
        variationHt,
        [Validators.required, Validators.pattern(this.variationPattern)]
      ],
      prixTtc: [
        prixTtc,
        [
          Validators.required,
          Validators.min(0),
          Validators.pattern(this.pricePattern)
        ]
      ],
      specialPriceTtc: [
        variantPlaceholder.specialPriceTtc,
        [Validators.min(0), Validators.pattern(this.pricePattern)]
      ],
      poids: [
        variantPlaceholder.poids,
        [
          Validators.required,
          Validators.min(1),
          Validators.pattern(this.metricPattern)
        ]
      ],
      quantite: [
        variantPlaceholder.quantite,
        [
          Validators.required,
          Validators.min(0),
          Validators.pattern(this.numericPattern)
        ]
      ],
      stockCritique: [
        variantPlaceholder.stockCritique,
        [
          Validators.required,
          Validators.min(0),
          Validators.pattern(this.numericPattern)
        ]
      ],
      attributValeurs: this.fb.array([
        ...variantPlaceholder.attributValeurs
      ])
    });

    control.push(newDeclinaison);
  }

