import {
    Component,
    OnInit,
    ViewChild,
    AfterViewInit,
    ChangeDetectorRef,
    NgZone
} from "@angular/core";
import { StoreService } from "../../../shared/services/store.service";
import { ActivatedRoute, Router } from "@angular/router";
import { ScriptService } from "../../../shared/services/script.service";
import { ReactCptComponent } from "../../../shared/components/react-cpt/react-cpt.component";
import { ProduitService } from "../../../api-shop";
import { environment } from "../../../../environments/environment";

import { configListings } from "./listingConfiguration";
import { PluginsService } from "../../../api-paiement";
import { formatDomain } from "../../../shared/utils/utils";
import { UserService } from "../../../api-member";
import { ErrorsService } from "../../../global/services/errors.service";
import { PermissionService } from "../../../shared/services/permission.service";

const { scheme, pathParent, apiProd } = environment;

@Component({
    selector: "app-listing",
    templateUrl: "./listing.component.html",
    styleUrls: ["./listing.component.scss"]
})
export class ListingComponent implements OnInit, AfterViewInit {
    module: string;

    rcpt;
    props = {};
    action = {};
    total = "";
    param = {
        action: "batch",
        obj: {
            ids: [],
            status: false
        }
    };
    domain = { name: "", title: "" };

    @ViewChild("rcptChild") rcptChild: ReactCptComponent;

    constructor(
        public store: StoreService,
        private route: ActivatedRoute,
        private router: Router,
        private scriptService: ScriptService,
        private cd: ChangeDetectorRef,
        private produitApi: ProduitService,
        private paiementPluginAPi: PluginsService,
        private memberApi: UserService,
        private NgZone: NgZone,
        private errorsService: ErrorsService,
        public permService: PermissionService
    ) { }

    ngOnInit() {
        this.route.paramMap.subscribe(params => {
            this.module = params["params"]["module"];
            this.scriptService.loadScript("js", "TableRcpt").then(() => {
                this.loadDatable();
                this.rcpt = window["TableRcpt"].default;
            });
        });
    }

    ngAfterViewInit() { }

    mitigatePath() {
        let listingPath = "";
        let addPath = "";
        let modulePath = "";
        switch (this.module) {
            case "produit":
                listingPath = "produit";
                addPath = "produits/produit";
                modulePath = "shop";
                break;

            case "formation":
                listingPath = "produit/formation";
                addPath = "produits/formations";
                modulePath = "shop";
                break;

            case "produitdemat":
                listingPath = "produit/demat";
                addPath = "produits/demats";
                modulePath = "shop";
                break;

            case "coupon":
                listingPath = "coupon";
                addPath = "coupons/coupon";
                modulePath = "shop";
                break;

            case "optionpanier":
                listingPath = "option/panier";
                addPath = "optionspanier/optionpanier";
                modulePath = "shop";
                break;

            case "formule":
                listingPath = "formule";
                addPath = "formules/formule";
                modulePath = "shop";
                break;
            // case "categorie":
            //     listingPath = "categorie";
            //     break;
            // case "declinaison":
            //     listingPath = "attribut";
            //     break;
            // case "fabricant":
            //     listingPath = "fabricant";
            //     break;
            // case "fournisseur":
            //     listingPath = "fournisseur";
            //     break;
            // case "messagedispo":
            //     listingPath = "message/dispo";
            //     break;
            // case "comment":
            //     listingPath = "comment";
            //     break;
            // case "tva":
            //     listingPath = "groupe/regle/taxe";
            //     break;
            case "transporteur":
                listingPath = "transporteur";
                addPath = "transporteurs/transporteur";
                modulePath = "shop";

                break;
            case "email":
                listingPath = "email";
                addPath = "emails/email";
                modulePath = "shop";
                break;

            case "commande":
                listingPath = "commande";
                addPath = "commandes/commande";
                modulePath = "shop";
                break;

            case "modedepaiement":
                listingPath = "moyen";
                addPath = "modedepaiement/modedepaiement";
                modulePath = "shop";
                break;

            case "user":
                listingPath = "user";
                addPath = "users/user";
                modulePath = "shop";
                break;

            case "userwifeo":
                listingPath = "user";
                addPath = "userswifeo/user";
                modulePath = "shop";
                break;
        }
        return { listing: listingPath, addPath: modulePath + "/" + addPath };
    }

    refreshList(e) {
        this.rcptChild.reRender();
    }

    loadDatable() {
        //console.log("loadDatable ", this.module);
        this.domain = { name: this.module, title: this.module };
        // console.log("loading datable...", this.mitigatePath());
        this.props["bearer"] = localStorage.getItem("currentUser");
        // const prePath = '';

        switch (this.module) {
            case "modedepaiement":
                this.domain.title = "modes de paiement";
                break;
            case "optionpanier":
                this.domain.title = "options panier";
                break;
            case "produitdemat":
                this.domain.title = "produits dématerialisé";
                break;
        }

        if (this.module == "commande" || this.module == "modedepaiement") {
            // this.paiementPluginAPi.apiPluginsAllGet().subscribe(e=>{
            //     console.log("Plugins : ", e);
            // });
            this.props["dataTableConf"] = null;
            let configs2 = configListings.conf.filter(
                msg => msg.module == "paiement"
            );
            let conf2Arr: any[] = configs2[0]["listings"];
            if (conf2Arr.filter(msg => msg.name == this.module).length > 0) {
                this.props["dataTableConf"] = conf2Arr.filter(
                    msg => msg.name == this.module
                )[0].conf;
            }
            //
            // this.props["url"] =
            //     scheme + "://paiement.epicblue.fr/api/magasins/" + this.store.paiementId.toString() + "/datatable/" + this.mitigatePath().listing;
            this.props["url"] = formatDomain(
                scheme +
                "://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/paiement/api/magasins/" +
                this.store.paiementId.toString() +
                "/datatable/" +
                this.mitigatePath().listing
            );
        }  else if (this.module == "domain") {
            this.props["dataTableConf"] = null;
            let configs2 = configListings.conf.filter(
                msg => msg.module == "domain"
            );
            let conf2Arr: any[] = configs2[0]["listings"];
            if (conf2Arr.filter(msg => msg.name == this.module).length > 0) {
                this.props["dataTableConf"] = conf2Arr.filter(
                    msg => msg.name == this.module
                )[0].conf;
            }

            this.props["url"] = formatDomain(
                scheme +
                "://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/platform/api/datatable/domain"
            );
            //console.log("domain props", this.props);
            // scheme + "://user."+ environment.domain +"/api/datatable/user";
        }else if (this.module == "user") {
            this.props["dataTableConf"] = null;
            let configs2 = configListings.conf.filter(
                msg => msg.module == "user"
            );
            let conf2Arr: any[] = configs2[0]["listings"];
            if (conf2Arr.filter(msg => msg.name == this.module).length > 0) {
                this.props["dataTableConf"] = conf2Arr.filter(
                    msg => msg.name == this.module
                )[0].conf;
            }

            this.props["url"] = formatDomain(
                scheme +
                "://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/membership/api/magasins/" +
                this.store.memberId.toString() +
                "/datatable/" +
                this.mitigatePath().listing
            );

            // this.props["url"] =
            //     scheme +
            //     "://membership.epicblue.fr/api/magasins/" +
            //     this.store.memberId.toString() +
            //     "/datatable/" +
            //     this.mitigatePath().listing;
            //this.props["url"] = scheme + "://user.epicblue.fr/api/user/me";
        } else if (this.module == "userwifeo") {
            this.props["dataTableConf"] = null;
            let configs2 = configListings.conf.filter(
                msg => msg.module == "user"
            );
            let conf2Arr: any[] = configs2[0]["listings"];
            if (conf2Arr.filter(msg => msg.name == this.module).length > 0) {
                this.props["dataTableConf"] = conf2Arr.filter(
                    msg => msg.name == this.module
                )[0].conf;
            }

            this.props["url"] = formatDomain(
                scheme +
                "://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/user/api/datatable/user"
            );
            // scheme + "://user."+ environment.domain +"/api/datatable/user";
        } else {
            let configs = configListings.conf.filter(
                msg => msg.module == "shop"
            );
            let confArr: any[] = configs[0]["listings"];
            if (confArr.filter(msg => msg.name == this.module).length > 0) {
                this.props["dataTableConf"] = confArr.filter(
                    msg => msg.name == this.module
                )[0].conf;
            }

            //console.log("Listing CPT : ", this.props, this.mitigatePath());

            // this.props["url"] =
            //     scheme + "://shop.epicblue.fr/api/magasins/" + this.store.shopId.toString() + "/datatable/" + this.mitigatePath().listing;
            this.props["url"] = formatDomain(
                scheme +
                "://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/shop/api/magasins/" +
                this.store.shopId.toString() +
                "/datatable/" +
                this.mitigatePath().listing
            );
        }

        let _thisRef = this;
        this.props["onAction"] = function (e) {
            //console.log('onAction from ngCpt', this, e);
            if (e.action == "selected" || e.action == "batch") {
                _thisRef.action = { action: e.action };
                _thisRef.param = e;
            } else if (e.action == "results") {
                _thisRef.total = e.obj.total.recordsTotal;
            } else if (e.action == "click") {
                //console.log("click classic", e);

                _thisRef.NgZone.run(() => {
                    _thisRef.router.navigate([
                        _thisRef.mitigatePath().addPath,
                        e.obj["id"]
                    ]);
                });
            } else if (e.action == "active") {
                //console.log("actIVATE ListING", e);
                _thisRef.produitApi
                    .apiMagasinsSlugProduitsActivesMultisPost(
                        [{ id: e.obj["id"] }],
                        _thisRef.store.shopId.toString()
                    )
                    .subscribe(e => {
                        //console.log(e);
                    });
            } else if (e.action == "catch_error") {
                _thisRef.handleErrorsAjax(e.data);
            }
            if (!_thisRef.cd["destroyed"]) {
                _thisRef.cd.detectChanges();
            }
        };
        //console.log(this.rcptChild);

        setTimeout(() => {
            if (typeof this.rcptChild != "undefined") {
                this.rcptChild.reRender();
            }
        }, 150);

        //
    }

    handleErrorsAjax(data) {
        const { xhr } = data;
        const { status, responseJSON } = xhr;
        let message = "";
        if (responseJSON) {
            message = responseJSON.message;
        }

        const error = {
            status,
            error: { message }
        };
        this.errorsService.handleErrorHttp(error);
    }
}



