import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { StoreService } from "../../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FabricantService } from "../../../../api-shop";

@Component({
    selector: "app-shop-fabricant-form",
    templateUrl: "./fabricant-form.component.html",
    styleUrls: ["./fabricant-form.component.scss"]
})
export class FabricantFormComponent implements OnInit {
    myForm: FormGroup;
    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() edited: EventEmitter<any> = new EventEmitter();
    @Input() fabricant;

    constructor(
        public store: StoreService,
        private fb: FormBuilder,
        private apiFabricant: FabricantService
    ) {}

    ngOnInit() {
        this.createForm();
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    createForm() {
        let nom = "";
        let description = "";
        let logo = "";

        if (this.fabricant.id) {
            nom = this.fabricant.nom;
            description = this.fabricant.description;
            logo = this.fabricant.logo;
        }

        this.myForm = this.fb.group({
            nom: [
                nom,
                [
                    Validators.required,
                    Validators.minLength(2),
                    Validators.maxLength(50)
                ]
            ],
            description: [
                description,
                [Validators.minLength(2), Validators.maxLength(1400)]
            ],
            logo: [logo, [Validators.minLength(2), Validators.maxLength(3000)]]
        });
    }

    async submitHandler() {
        let formValue = this.myForm.value;
        let id = null;

        if (this.fabricant.id) {
            id = this.fabricant.id;
            this.apiFabricant
                .apiMagasinsSlugFabricantsFabricantPut(
                    {
                        nom: formValue.nom,
                        description: formValue.description,
                        logo: formValue.logo
                    },
                    this.store.shopId.toString(),
                    id
                )
                .subscribe(e => {
                    this.edited.emit({
                        id: id,
                        nom: formValue.nom,
                        description: formValue.description,
                        logo: formValue.logo
                    });
                    this.myForm.reset();
                    this.createForm();
                });
        } else {
            this.apiFabricant
                .apiMagasinsSlugFabricantsPost(
                    {
                        nom: formValue.nom,
                        description: formValue.description,
                        logo: formValue.logo
                    },
                    this.store.shopId.toString()
                )
                .subscribe(e => {
                    this.added.emit({
                        id: e.id,
                        nom: formValue.nom,
                        description: formValue.description,
                        logo: formValue.logo
                    });
                    this.myForm.reset();
                    this.createForm();
                });
        }
    }
}



