    addTranche(zt) {
        const control = <FormArray>zt.controls["tranchePoidsPrixs"];
        let px = 0;
        let poidsMin = 0;
        let poidsMax = 0;
        let prixMin = 0;
        let prixMax = 0;
        let description = "";

        if (control.controls.length > 0) {
            let previous = control.controls[control.controls.length - 1].value;
            px = previous.prixHt;
            poidsMin = previous.poidsMax + 1;
            poidsMax = previous.poidsMax + 2;
            prixMin = previous.prixMax + 1;
            prixMax = previous.prixMax + 2;
            description = previous.description;
        }

        control.push(
            this.initTranchesForm({
                prixHt: px,
                prixMin: prixMin,
                prixMax: prixMax,
                poidsMin: poidsMin,
                poidsMax: poidsMax,
                description: description
            })
        );
    }

    isFieldValid(form, field: string) {
        if (!form.get(field)) {
            return null;
        } else {
            return !form.get(field).valid;
        }
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": this.isFieldValid(form, field)
            //'we-invalid-feedback': this.isFieldValid(form, field)
        };
    }

