    removeZone(i) {
        for (let b = 0; b < this.transporteur["zoneTransporteurs"][i].regions.length; b++) {
            let regions = this.transporteur["zoneTransporteurs"][i]["regions"][b];

            let selected = 0;
            let total = regions.countries.length;

            for (let c = 0; c < regions["countries"].length; c++) {
                if (regions.countries[c].selected) {
                    this.selectable[b]["countries"][c].selected = false;
                    selected++;
                }
            }
            if (selected == total) {
                this.selectable[b].selected = false;
            }
        }

        const control = <FormArray>this.transporteurForm.controls["zoneTransporteurs"];
        control.removeAt(i);

        this.cd.detectChanges();
        this.transporteur["zoneTransporteurs"].splice(i, 1);
        this.dropdownStates();
    }

    dropdownStates() {
        this.arrT = new Array(this.transporteur["zoneTransporteurs"].length);
        for (let d = 0; d < this.arrT.length; d++) {
            this.arrT[d] = { open: false };
        }
    }

