import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { MainComponent } from "./components/main/main.component";
import { AccueilComponent } from "./components/accueil/accueil.component";
import { ListingComponent } from "./components/listing/listing.component";
import { ProduitFormComponent } from "./components/produit/form/produit-form.component";
import { CouponFormComponent } from "./components/coupon/form/coupon-form.component";
import { OptionPanierFormComponent } from "./components/optionpanier/form/optionpanier-form.component";
import { EmailFormComponent } from "./components/email/form/email-form.component";
import { TransporteurFormComponent } from "./components/transporteur/form/transporteur-form.component";
import { PreferenceFormComponent } from "./components/preference/form/preference-form.component";
import { TestFicheProduitComponent } from "./components/test-fiche-produit/test-fiche-produit.component";
import { PaiementComponent } from "./components/paiement/paiement.component";
import { ModedepaiementComponent } from "./components/modedepaiement/modedepaiement.component";
import { ModedepaiementFormComponent } from "./components/modedepaiement-form/modedepaiement-form.component";
import { ApercuProduitComponent } from "./components/apercu-produit/apercu-produit.component";
import { PaiementFormComponent } from "./components/paiement-form/paiement-form.component";
import { UserFormComponent } from "./components/user/user-form/user-form.component";
import { ApercuFactureComponent } from "./components/apercu-facture/apercu-facture.component";
import { FormuleFormComponent } from "./components/formule/formule-form/formule-form.component";
import { WifeoUserFormComponent } from "./components/wifeo-user/wifeo-user-form/wifeo-user-form.component";
import { ProduitDematFormComponent } from './components/produit-demat-form/produit-demat-form.component';
import { ProduitFormationComponent } from './components/produit-formation/produit-formation.component';
import { CursusFormationComponent } from './components/cursus-formation/cursus-formation.component';
const routes: Routes = [
    {
        path: "",
        component: MainComponent,
        children: [
            { path: "dashboard", component: AccueilComponent },

            // { path: "list/paiement", component: PaiementComponent },
            // { path: "paiement/paiement", component: PaiementFormComponent},
            {
                path: "commandes/commande/:id",
                component: PaiementFormComponent
            },
            { path: "users/user/:id", component: UserFormComponent },
            { path: "userswifeo/user/:id", component: WifeoUserFormComponent },

            {
                path: "modedepaiement/modedepaiement",
                component: ModedepaiementFormComponent
            },
            {
                path: "modedepaiement/modedepaiement/:id",
                component: ModedepaiementFormComponent
            },
            { path: "list/:module", component: ListingComponent },
            { path: "emails/email/:id", component: EmailFormComponent },
            { path: "emails/email", component: EmailFormComponent },
            {
                path: "transporteurs/transporteur/:id",
                component: TransporteurFormComponent
            },
            {
                path: "transporteurs/transporteur",
                component: TransporteurFormComponent
            },

            { path: "coupons/coupon/:id", component: CouponFormComponent },
            { path: "coupons/coupon", component: CouponFormComponent },

            {
                path: "optionspanier/optionpanier/:id",
                component: OptionPanierFormComponent
            },
            {
                path: "optionspanier/optionpanier",
                component: OptionPanierFormComponent
            },

            { path: "formules/formule/:id", component: FormuleFormComponent },
            { path: "formules/formule", component: FormuleFormComponent },

            { path: "produits/produit/:id", component: ProduitFormComponent }, 
            {
                path: "produits/produit",
                component: ProduitFormComponent
            },
            { path: "produits/demats/:id", component: ProduitDematFormComponent }, 
            {
                path: "produits/demats",
                component: ProduitDematFormComponent
            },   
            { path: "produits/formations/:id", component: ProduitFormationComponent }, 
            { path: "produits/formations/:id/cursus", component: CursusFormationComponent }, 
            {
                path: "produits/formations",
                component: ProduitFormationComponent
            },                       

            { path: "preference", component: PreferenceFormComponent },
            // { path: "test_fiche_produit", component: TestFicheProduitComponent },
            // { path: "apercu/produit/:id", component: ApercuProduitComponent },
            { path: "apercu/produit/:id", component: ApercuProduitComponent },
            { path: "apercu/produit", component: ApercuProduitComponent },
            { path: "apercu/facture", component: ApercuFactureComponent },
            { path: "**", redirectTo: "dashboard" }
            // { path: "**", redirectTo: "list/produit" }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ShopRoutingModule {}



