import { Injectable } from "@angular/core";
import { PermissionService } from "../../shared/services/permission.service";
import { StatsService } from "../../api-shop";
import { StoreService } from "../../shared/services/store.service";

@Injectable()
export class ShopService {
    nbProducts = 0;
    nbFormations = 0;

    constructor(
        private permService: PermissionService,
        private statsApi: StatsService,
        private store: StoreService
    ) {}


    hasReachedMaxNbFormations(openModal: boolean = true) {

        console.log("Nb formations :", this.nbFormations,  this.permService.LIMIT.NB_FORMATIONS);
        const hasReached = this.permService.hasReachedLimit(
            this.permService.LIMIT.NB_FORMATIONS,
            this.nbFormations
        );
        if (hasReached && openModal) {
            this.permService.openModalPlan(
                this.permService.LIMIT.NB_FORMATIONS
            );
        }
        return hasReached;
    }

    hasReachedMaxNbProducts(openModal: boolean = true) {

        //console.log("Nb products :", this.nbProducts,  this.permService.LIMIT.NB_PRODUITS_SHOP);
        const hasReached = this.permService.hasReachedLimit(
            this.permService.LIMIT.NB_PRODUITS_SHOP,
            this.nbProducts
        );
        if (hasReached && openModal) {
            this.permService.openModalPlan(
                this.permService.LIMIT.NB_PRODUITS_SHOP
            );
        }
        return hasReached;
    }

    fetchNbProducts(onSuccess: Function) {
        // api // on doit le savoir au chargement du module
        this.statsApi
            .apiMagasinsSlugProductTotalGet(this.store.shopId)
            .subscribe(result => {
                //console.log("fetchNbProducts :", result);
                this.nbProducts = result['produits'];
                this.nbFormations = result['formations'];

                onSuccess();
            });
    }
}



