        const msg = JSON.parse(localStorage.getItem("msg")) || {};

        const isWWWsite =
            msg &&
            msg.site &&
            msg.site.domains &&
            msg.site.domains.find(d => d.name === `www.${environment.domain}`); 


        if (isWWWsite) {
            const idxCommandes = this.menuItems.findIndex(
                mi => mi.name === "Commandes"
            );
            const idxProduits = this.menuItems.findIndex(
                mi => mi.name === "Produits"
            );
            this.menuItems[idxCommandes].children.push({
                className: this.cssMenuItem,
                name: "Utilisateurs Wifeo",
                linkFill: true,
                routerLink: "/shop/list/userwifeo",
                type: "page"
            });
            this.menuItems[idxCommandes].children.push({
                className: this.cssMenuItem,
                name: "Domaines Wifeo",
                linkFill: true,
                routerLink: "/shop/list/domain",
                type: "page"
            });
            this.menuItems[idxProduits].children.push({
                className: this.cssMenuItem,
                name: "Formules",
                linkFill: true,
                routerLink: "/shop/list/formule",
                type: "page"
            });


        }


