import {
    Component,
    OnInit,
    ViewEncapsulation,
    ChangeDetectorRef
} from "@angular/core";
import { StoreService } from "../../../shared/services/store.service";
import { ModuleService } from "../../../global/services/module.service";
import { ShopService } from "../../services/shop.service";
import { ActivatedRoute, ActivatedRouteSnapshot, CanActivate, NavigationEnd, NavigationStart, Router } from "@angular/router";
import 'rxjs/add/operator/filter';
import { MenuItem } from "../../../shared/classes/menu";

@Component({
    selector: "app-main",
    templateUrl: "./main.component.html",
    styleUrls: ["./main.component.scss", "../../shop.scss"],
    encapsulation: ViewEncapsulation.None
})
export class MainComponent implements OnInit {
    shopLoaded: boolean = false;
    componentType; //ClassName du component actif dans le routing
    alernateSidebar: boolean = false;

    itemsFormationCats: MenuItem[] = [
        {
            id: "formation_list",
            iconL: { active: true, className: "fal fa-copy" },
            iconR: {
                active: true,
                className:
                    " we-btn-icon we-align-self-center text-greyX fal fa-angle-right"
            },
            name: "Vos formations",
            routerLink: "/shop/list/formation",
            className: "we-sidebar-nav-item we-sidebar-nav-link we-w-100",
            linkFill: true,
            type: "page"
        }
    ];

    constructor(
        private store: StoreService,
        private moduleService: ModuleService,
        private shopService: ShopService,
        private route: ActivatedRoute,
        private router: Router
    ) {

        //utilisé pour setter la sidebar de gauche dans l'admin
        this.router.events
            .filter(event => event instanceof NavigationEnd)
            .subscribe((event: NavigationEnd) => {
                if ("/shop/list/formation" === event.url) {
                    this.alernateSidebar = true;
                } else if ("/shop/produits/formations/" === event.url.substring(0, 26)) {
                    this.alernateSidebar = true;
                } else {
                    this.alernateSidebar = false;
                }
            });
    }





    ngOnInit() {
        const hasShop = this.store.curSite.uids.find(u => u.nom === "shop");
        this.moduleService.activateModule("shop").then(() => {
            this.shopService.fetchNbProducts(() => {
                if (!hasShop) {
                    this.moduleService.initDesignShop(this.store.shopId, () => {
                        this.shopLoaded = true;
                    });
                } else {
                    this.shopLoaded = true;
                }
            });
        });
    }
}



