import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from "@angular/core";

import { FormGroup, Validators, FormBuilder, FormArray } from "@angular/forms";
import { StoreService } from "../../../../shared/services/store.service";
import { ProduitPersonnaliseService } from "../../../../api-shop";
import { FormulairesService } from "../../../../api-form";
import { ScriptService } from "../../../../shared/services/script.service";
import { ReactCptComponent } from "../../../../shared/components/react-cpt/react-cpt.component";
declare var moment: any;


@Component({
  selector: "app-shop-personnalisation-builder",
  templateUrl: "./personnalisation-builder.component.html",
  styleUrls: ["./personnalisation-builder.component.scss"]
})
/**
 * Generateur de personnalisation produit
 */
export class PersonnalisationBuilderComponent implements OnInit {

  @ViewChild("rcptFormCustom") rcptFormCustom: ReactCptComponent;


  constructor(
    public store: StoreService,
    private scriptService: ScriptService,
    private fb: FormBuilder,
    private personnaliseApi: ProduitPersonnaliseService,

  ) { }

  @Input() persos; //Toutes les perso disponibles
  @Input() persosFiltered; //Perso Selectionnée dans le select
  @Output() selected: EventEmitter<any> = new EventEmitter(); //Event vers parent de la perso selectionne

  rcptComponent: any; //le component rcpt dans la window
  rcptFormProps: any = {}; //Les props du component
  rcptloaded = false;  //Le component rcpt est il loadé
  nameForm = ""; //Nom du formulaire form input
  inEdit = false; // Le select est editable
  pendingParams = {}; // les params a mettre à jour http quand le rcpt est modifié
  isMakingRequest = false; // Une requete http est elle en cours

  // Event qui permet de faire remonter les infos du rcpt form
  actions = {
    updatePropsForm: $event => this.updatePropsForm($event.params),
  };


  //event sur le select de la personnalisation
  onPersoChange(v) {

    this.persosFiltered = v;

    //console.log("Perso change", v);
   

    if (v != "Créer une personnalisation") {
      this.selected.emit(this.persosFiltered);
      this.rcptloaded = false;
      this.inEdit = false;

      setTimeout(() => {
        var fields = (typeof this.persosFiltered['fields'] != "string") ? this.persosFiltered['fields'] : JSON.parse(this.persosFiltered['fields']);
        //alert("Loading "+ v.nom + "formItems:"+ fields["formItems"].length );
        this.nameForm = v.nom;
        this.loadRcptFormEditor(fields);
        this.inEdit = true;
      }, 200);



    } else {
      this.selected.emit(this.persosFiltered);
      this.rcptloaded = false;
      this.inEdit = false;
    }
  }

  //update depuis le rcpt form-editor
  updatePropsForm(params) {
    this.pendingParams = { schema: params.schema, uiSchema: params.uiSchema, formItems: params.state.formItems, ver: "0.0.1" };
  }


  //creation d'une nouvelle personnalisation
  createPerso() {

    var fields = {
      "schema": {
        "title": "",
        "description": "",
        "type": "object",
        "properties": {
          "text_cbb92f23-cf93-4b51-9869-416faf4910ab": {
            "minLength": 1,
            "maxLength": 50,
            "type": "string",
            "title": "Nom"
          }
        },
        "required": [
          "text_cbb92f23-cf93-4b51-9869-416faf4910ab",
        ]
      },
      "uiSchema": {
        "Message": {
          "ui:widget": "textarea"
        },
        "text_cbb92f23-cf93-4b51-9869-416faf4910ab": {
          "ui:widget": "text",
          "ui:placeholder": "motif,sujet,suggestion",
          "ui:help": "",
          "ui:title": "Nom"
        },
        "ui:order": [
          "text_cbb92f23-cf93-4b51-9869-416faf4910ab",

          "*"
        ],
        "testCheckbox": {
          "ui:widget": "radio"
        }
      },
      "formItems": [
        {
          "type": "text",
          "labelMenu": "Texte court",
          "label": "Nom",
          "className": "form-control",
          "name": "text_cbb92f23-cf93-4b51-9869-416faf4910ab",
          "required": true,
          "placeholder": "motif,sujet,suggestion",
          "helpText": "",
          "minLength": 1,
          "maxLength": 50
        }
      ],
      "ver": "0.0.1"
    };

    var newForm = {
      nom: "Nouvelle personnalisation du " + moment().format("DD/MM/YYYY HH:mm"),
      fields: fields
    }
    this.persos.push(newForm);
    newForm = this.persos[this.persos.length - 1];


    this.nameForm = newForm.nom;
    this.loadRcptFormEditor(fields);
    this.persosFiltered = newForm;
    this.inEdit = true;

  }


  //Enregistrer la modification
  savePerso() {

    var fields = this.pendingParams;
    this.isMakingRequest = true;

    if (JSON.stringify(this.pendingParams) == '{}') {
      return false;
    }

    if (!this.persosFiltered['id']) {
      this.personnaliseApi.apiMagasinsSlugProduitsPersonnalisesPost(
        {
          nom: this.nameForm,
          infosJson: JSON.stringify(fields)
        },
        this.store.shopId.toString()).subscribe(e => {
          let index = this.persos.indexOf(this.persosFiltered);
          //(e.nom +"index :" + index );
          this.persos[index] = { "nom": e.nom, "id": e.id, "fields": e.infosJson };
          this.persosFiltered = this.persos[index];
          this.selected.emit(this.persosFiltered);
          this.isMakingRequest = false;
          this.pendingParams = {};
        });

    } else {
      this.personnaliseApi.apiMagasinsSlugProduitsProduitPersonnalisePersonnalisePut(
        {
          nom: this.nameForm,
          infosJson: JSON.stringify(fields)
        },
        this.store.shopId.toString(), this.persosFiltered['id']).subscribe(e => {
          let index = this.persos.indexOf(this.persosFiltered);
          //alert(e.nom + "index :" + index );
          this.persos[index] = { "nom": e.nom, "id": e.id, "fields": e.infosJson };
          this.persosFiltered = this.persos[index];
          this.selected.emit(this.persosFiltered);
          this.isMakingRequest = false;
          this.pendingParams = {};
        });
    }
  }


  //Suppression de la personnalisation
  onPersoDelete() {


    this.isMakingRequest = true;
    let index = this.persos.indexOf(this.persosFiltered);
    if (index > -1) {

      if (this.persos[index].id) {
        this.personnaliseApi.apiMagasinsSlugProduitsProduitPersonnalisePersonnaliseDelete(this.store.shopId.toString(), this.persos[index].id).subscribe(e => {
          this.persos.splice(index, 1);
          this.persosFiltered = "Sélectionner";
          this.selected.emit({});
          this.inEdit = false;
          this.rcptloaded = false;
          this.isMakingRequest = false;
          this.pendingParams = {};
        });
      }

    }

  }


  //le champ est il valid
  isFieldValid(form, field: string) {
    return ((!form.get(field).valid));
  }

  //Css erreur
  displayFieldCss(form, field: string) {
    return {
      'we-is-invalid': this.isFieldValid(form, field),
      //'we-invalid-feedback': this.isFieldValid(form, field)
    };
  }


  //Bind les props au rcpt formulaires
  loadRcptFormEditor(fields) {
    this.rcptFormProps = {
      "mode": "edit",
      "typeForm": "custom",
      "addEmail": false,
      "externalCta": true,
      "prefixCss": "",
      "mep": "stack-reverse",
      "sectionCss": "",
      "integrated": true,
      "containerCss": "container",
      "loadingMode": "remote",
      "staticForm": fields,
      "blocks": [
        {
          "name": "cta",
          "cta": {
            "txt": "Nous contacter",
            "css": "",
            "class": "btn-primary",
            "link": "/",
            "tpl": "BtnBootstrap",
            "visible": false,
            "icon": {
              "active": true,
              "css": "far fa-envelope"
            }
          }
        },
        {
          "name": "title",
          "title": {
            "txt": "<h1 style=\"text-align:center;\"></h1>",
            "class": "",
            "visible": false
          }
        },
        {
          "name": "description",
          "description": {
            "txt": "Pour toutes vos questions, veuillez remplir le formulaire ci-dessous"
          }
        }
      ],
      "nameForm": "",
      "onAction": this.actions.updatePropsForm
    };
    this.rcptloaded = true;

    if (this.rcptFormCustom) {
      this.rcptFormCustom.binding();
      this.rcptFormCustom.reRender();

    }
  }


  //Compare les persos ds le select
  compareFn = this._compareFn.bind(this);
  _compareFn(a, b) {
    if (typeof a == "undefined" || typeof b == 'undefined') {
      return false;
    }
    if (!a || !b) {
      return false;
    }
    if (typeof a.id == "undefined" || typeof b.id == 'undefined') {
      return false;
    }
    return a.id === b.id;
  }


  //Chargement des rcpts dans la window + la perso courante
  ngOnInit() {

    if (this.persosFiltered) {

      if (typeof this.persosFiltered['fields'] != "undefined") {
        var fields = (typeof this.persosFiltered['fields'] != "string") ? this.persosFiltered['fields'] : JSON.parse(this.persosFiltered['fields']);
        this.scriptService
          .loader("JsonSchemaForm", "FormEditorRcpt")
          .then(() => {
            this.rcptComponent = window['FormEditorRcpt']['default'];
            this.nameForm = this.persosFiltered.nom;
            this.loadRcptFormEditor(fields);
            this.inEdit = true;
          });
      } else {
        this.scriptService
          .loader("JsonSchemaForm", "FormEditorRcpt")
          .then(() => {
            this.rcptComponent = window['FormEditorRcpt']['default'];
            this.persosFiltered = "Créer une personnalisation";
            //this.createPerso();
          });
      }


    } else {
      this.scriptService
        .loader("JsonSchemaForm", "FormEditorRcpt")
        .then(() => {
          this.rcptComponent = window['FormEditorRcpt']['default'];
          this.persosFiltered = "Créer une personnalisation";
          //this.createPerso();
        });
    }


  }



}



