import { Component, OnInit } from "@angular/core";

import { FormBuilder, FormGroup, FormControl, Validators, FormArray, ValidationErrors } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { EmailService } from "../../../../api-shop";
import { StoreService } from "../../../../shared/services/store.service";
import { scrollTo } from "../../../../shared/utils/utils";

@Component({
    selector: "app-shop-email-form",
    templateUrl: "./email-form.component.html",
    styleUrls: ["./email-form.component.scss"]
})
export class EmailFormComponent implements OnInit {
    emailForm: FormGroup;
    metricPattern = /^\d+([,.]\d{1,2})?$/;
    pricePattern = /^\d+([,.]\d{1,6})?$/;
    variationPattern = /^-?\d+([.,]\d{1,6})?$/;
    numericPattern = /^[0-9]*$/;
    referenceChoix = {};
    isLoading = false;

    keywords = [
        { keyword: "UTILISATEUR", explanation: "Affiche le nom complet de votre client" },
        {
            keyword: "INFOS_PAIEMENT",
            explanation: "Affiche l'adresse à laquelle envoyer le chèque et les espèces ou les coordonnées bancaires pour l'envoi d'un virement"
        },
        { keyword: "MONTANT", explanation: "Affiche le montant total de la commande" },
        { keyword: "REFERENCE_COMMANDE", explanation: "Affiche la référence de la commande" },

        { keyword: "COMMANDE_DETAILLEE", explanation: "Affiche le détail de sa commande " },
        { keyword: "FACTURE", explanation: "Affiche le lien vers la facture du client" },

        { keyword: "DATE_EXPEDITION", explanation: "Affiche la date d'expédition " },
        { keyword: "CODE_SUIVI_COLIS", explanation: "Affiche le code de suivi de colis" },
        { keyword: "", explanation: "" }
    ];

    defaultEmails = [
        {
            reference: "Paiement en attente",
            objet: "Commande crée",
            copie: 0,
            texte:
                "Bonjour [[UTILISATEUR]] ! \n\nVoici les informations de paiement pour la commande [[REFERENCE_COMMANDE]] \n\n[[INFOS_PAIEMENT]] \n\nMontant total : [[MONTANT]] \n\nMerci !",
            help: [this.keywords[0], this.keywords[1], this.keywords[2], this.keywords[3]]
        },
        {
            reference: "Paiement validé",
            objet: "Récapitulatif du paiement",
            copie: "",
            texte:
                "Bonjour [[UTILISATEUR]] !\n\nNous avons bien reçu votre paiement et nous vous en remercions.\n\nVoici le récapitulatif de votre commande :\n\n[[COMMANDE_DETAILLEE]]\n\nVous pouvez accéder à la facture par ici: [[FACTURE]]\n\nMerci !",
            help: [this.keywords[0], this.keywords[4], this.keywords[5]]
        },
        {
            reference: "Commande expédiée",
            objet: "Commande traitée et expédiée",
            copie: "",
            texte:
                "Bonjour [[UTILISATEUR]] !\n\nVotre commande vient d'être expédiée le [[DATE_EXPEDITION]]!\n\nVoici le récapitulatif de votre commande :\n\n[[COMMANDE_DETAILLEE]]\n\nVoici le code de suivi du colis: [[CODE_SUIVI_COLIS]]\n\nMerci !",
            help: [this.keywords[0], this.keywords[4], this.keywords[6], this.keywords[7]]
        }
    ];

    isEmailLoaded = false;
    Id = "";

    constructor(
        private fb: FormBuilder,
        private route: ActivatedRoute,
        private emailApi: EmailService,
        private router: Router,
        private store: StoreService
    ) {}

    compareby(item1, item2) {
        if (!item2) {
            return false;
        }
        return item1.reference == item2.reference;
    }

    dataChanged($event) {
        //console.log($event);
        this.createForm($event);
    }

    onSubmit() {
        if (this.emailForm.valid) {
            this.isLoading = true;
            //console.log(this.emailForm.valid, this.emailForm.value);

            let emailClone = { ...this.emailForm.value };
            delete emailClone["help"];

            if (this.Id) {
                this.emailApi.apiMagasinsSlugEmailsEmailPut(emailClone, this.store.shopId.toString(), this.Id).subscribe(t => {
                    //console.log("updated :", t);
                    this.isLoading = false;
                });
            } else {
                this.emailApi.apiMagasinsSlugEmailsPost(emailClone, this.store.shopId.toString()).subscribe(t => {
                    // console.log("created", t);
                    this.Id = t.id.toString();
                    this.router.navigate(["shop/emails/email/" + t.id]);
                    this.isLoading = false;
                });
            }
        } else {
            scrollTo(["we-is-invalid", "dropdown-error"]);
        }
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    toggleCopie() {
        this.emailForm.patchValue({ copie: !this.emailForm.get("copie").value });
    }

    createForm(obj) {
        this.emailForm = this.fb.group({
            reference: [obj.reference, [Validators.required]],
            objet: [obj.objet, [Validators.required]],
            copie: [obj.copie, [Validators.min(0), Validators.max(1), Validators.required]],
            texte: [obj.texte, Validators.required],
            help: [obj.help, Validators.required]
        });
    }

    ngOnInit() {
        console.log("TODO : RETIRER le BTN AJOUT DU LISTING si EMAIl > 2");

        let defaultEmailsClone = JSON.parse(JSON.stringify(this.defaultEmails));

        this.emailApi.apiMagasinsSlugEmailsGet(this.store.shopId.toString()).subscribe(e => {
            for (let em = 0; em < e.length; em++) {
                let ref = this.defaultEmails.filter(h => h.reference == e[em].reference)[0];
                let i = this.defaultEmails.indexOf(ref);
                this.defaultEmails.splice(i, 1);
            }

            this.Id = this.route.snapshot.params.id;
            if (this.route.snapshot.params.id) {
                this.emailApi.apiMagasinsSlugEmailsEmailGet(this.store.shopId.toString(), this.Id).subscribe(r => {
                    this.defaultEmails.push(defaultEmailsClone.filter(h => h["reference"] == r.reference)[0]);
                    r["help"] = defaultEmailsClone.filter(h => h["reference"] == r.reference)[0].help;
                    this.referenceChoix = defaultEmailsClone.filter(h => h["reference"] == r.reference)[0];
                    this.createForm(r);
                    this.isEmailLoaded = true;
                });
            } else {
                if (this.defaultEmails.length == 0) {
                    alert("Vous ne pouvez plus ajouter de nouveau email. Modifier les gabarits existants.");
                } else {
                    this.createForm(this.defaultEmails[0]);
                    this.referenceChoix = this.defaultEmails[0];
                    this.isEmailLoaded = true;
                }
            }
        });
    }
}



