import {
    Component,
    OnInit,
    Input,
    ChangeDetectorRef,
    ViewChild,
    Output,
    EventEmitter
} from "@angular/core";
import { ModalComponent } from "../../../../../shared/components/modal/modal.component";
import {
    FormBuilder,
    FormControl,
    Validators,
    FormGroup,
    ValidatorFn,
    AbstractControl
} from "@angular/forms";
import { ProduitService } from "../../../../../api-shop";
import { StoreService } from "../../../../../shared/services/store.service";
import { Router } from "@angular/router";

@Component({
    selector: "app-form-list-produit-stock",
    templateUrl: "./form-list-produit-stock.component.html",
    styleUrls: ["./form-list-produit-stock.component.scss"]
})
export class FormListProduitStockComponent implements OnInit {
    @Input() table: { head: string[]; body: any[] };
    @Input() showStock: boolean;
    @Output() stockChange: EventEmitter<any> = new EventEmitter();
    @ViewChild(ModalComponent) modal: ModalComponent;

    productSelected = null;
    showModal = false;

    formsInputStock: { id: number; fg: FormGroup }[] = [];

    selectedRow = null;
    addStock = 0;
    pendingRequest = false;

    constructor(
        private cd: ChangeDetectorRef,
        private fb: FormBuilder,
        private produitApi: ProduitService,
        private store: StoreService,
        private router: Router
    ) {}

    ngOnInit() {
        this.initForm();
    }

    clickBtnUpdate(row) {
        const formInput = this.formsInputStock.find(f => f.id === row.id);
        if (!formInput.fg.valid) {
            return;
        }
        this.selectedRow = row;
        this.addStock = formInput.fg.get("addStock").value;
        // this.productSelected =
        this.showModal = true;
        this.cd.detectChanges(); // remplie l'instance de la modal apres le showModal
        this.modal.open();
    }

    addToStock() {
        if (this.pendingRequest) {
            return;
        }
        this.pendingRequest = true;
        this.produitApi
            .apiMagasinsSlugProduitsProduitAddsAddToStockToStockPut(
                this.addStock,
                String(this.store.shopId),
                this.selectedRow.id
            )
            .subscribe(() => {
                this.formsInputStock = this.formsInputStock.filter(
                    f => f.id !== this.selectedRow.id
                );
                this.table.body = this.table.body.filter(
                    r => r.id !== this.selectedRow.id
                );
                this.showModal = false;
                this.selectedRow = null;
                this.stockChange.emit();
                this.pendingRequest = false;
                this.cd.detectChanges();
            });
    }

    initForm() {
        this.table.body.forEach(row => {
            this.formsInputStock.push({
                id: row.id,
                fg: this.fb.group({
                    addStock: new FormControl(0, [
                        Validators.required,
                        Validators.min(1),
                        Validators.max(10000000)
                    ])
                })
            });
        });
    }

    cleanNumber($event, row) {
        this.formsInputStock
            .find(f => f.id == row.id)
            .fg.setValue({ addStock: parseInt($event.target.value, 10) });
        this.cd.detectChanges();
    }

    goToProduct(row) {
        this.router.navigate(["shop", "produits", "produit", row.id]);
    }
}



