import { Component, OnInit, ViewChild } from "@angular/core";
import { StoreService } from "../../../../shared/services/store.service";
import { ReactCptComponent } from "../../../../shared/components/react-cpt/react-cpt.component";

import { FormBuilder, FormGroup, FormControl, Validators, FormArray, ValidationErrors } from "@angular/forms";
import { OptionpanierService, GrtService, PreferenceService } from "../../../../api-shop";
import { ActivatedRoute, Router } from "@angular/router";
import { scrollTo } from "../../../../shared/utils/utils";

@Component({
    selector: "app-shop-optionpanier-form",
    templateUrl: "./optionpanier-form.component.html",
    styleUrls: ["./optionpanier-form.component.scss"]
})
export class OptionPanierFormComponent implements OnInit {
    optionPanierForm: FormGroup;

    numericPattern = /^[0-9]*$/;

    metricPattern = /^\d+([,.]\d{1,2})?$/;
    pricePattern = /^\d+([,.]\d{1,6})?$/;
    variationPattern = /^-?\d+([.,]\d{1,6})?$/;

    showHt = true;

    @ViewChild(ReactCptComponent) rcptDate: ReactCptComponent;

    props: any;
    showTvaDropdown = false;
    tvaDropdownPlaceholder = "Sélectionner";
    originalPrice = "0";

    selectedItems = {
        tva: [],
        categorie: []
    };

    existingVariations = [];
    tvas = [];

    modules = [
        { name: "tva", toggle: "tvaDropdownPlaceholder", field: "libelle", dropdown: "showTvaDropdown", props: "groupeRegleTaxe", required: true }
    ];

    fakeproduit = {
        id: 1,
        reference: "Emballage cadeau",
        description: "Superbe papier cadeau de Noel esprit chatoyant type ecrin prestige edition deluxe",
        titre: "Emballage cadeau",
        imageUrl:
            "https://mediacache.epicred.fr/BtWXm8htRSwTswXUIjuaKTUkvzw=/1280x/https://media.epicred.fr/media/cache/original/all/5c9c9f4d8694d.jpg",
        prixHt: 5.5,
        illimited: true,
        isOption: true,
        freeAfter: 0,
        specialPriceTtc: 0,
        variationHt: 0
    };

    produit = {};
    isLoading = false;
    Id = null;
    isProduitLoaded = false;
    isTvaLoaded = false;
    isAE = false;
    devise = "EUR";

    constructor(
        public store: StoreService,
        private fb: FormBuilder,
        private optionPanierApi: OptionpanierService,
        private tvaApi: GrtService,
        private route: ActivatedRoute,
        private router: Router,
        private preferenceApi: PreferenceService
    ) {}

    togglePrice(v) {
        this.showHt = v;
    }

    onSubmit() {
        if (this.optionPanierForm.valid) {
            this.isLoading = true;
            let optP = { ...this.optionPanierForm.value };

            //console.log("transform before :", optP);

            if (!this.isAE) {
                optP["groupeRegleTaxe"] = this.optionPanierForm.get("groupeRegleTaxe").value[0].id;
            } else {
                delete optP["groupeRegleTaxe"];
            }

            optP["specialPriceTtc"] = 0;
            optP["variationHt"] = 0;
            optP["quantite"] = 0;
            optP["stockCritique"] = 0;
            optP["poids"] = 0;
            optP["conditionnement"] = 0;
            optP["isOption"] = true;

            optP["formulePaymentReccurent"] = 1;
            optP["formulePaymentFreq"] = 1;
            optP["formuleDuration"] = 1;
            optP["formuleGroupe"] = '[]';
            optP["active"] = 1;


            //console.log("TRANSFORM BEFORE POST: ", optP);

            if (this.Id) {
                this.optionPanierApi.apiMagasinsSlugOptionsProduitPanierPut(optP, this.store.shopId.toString(), this.Id).subscribe(e => {
                    //console.log(e);
                    this.isLoading = false;
                });
            } else {
                this.optionPanierApi.apiMagasinsSlugOptionsPaniersPost(optP, this.store.shopId.toString()).subscribe(e => {
                    //console.log(e);
                    this.Id = e.id;
                    this.router.navigate(["shop/optionspanier/optionpanier/" + e.id]);
                    this.isLoading = false;
                });
            }
        } else {
            console.log("invalid form", this.getFormValidationErrors(this.optionPanierForm));
            scrollTo(["we-is-invalid", "dropdown-error"]);
        }
    }

    getFormValidationErrors(f) {
        Object.keys(f.controls).forEach(key => {
            console.log(key, f.get(key).valid);
            const controlErrors: ValidationErrors = f.get(key).errors;
            if (controlErrors != null) {
                Object.keys(controlErrors).forEach(keyError => {
                    console.log("Key control: " + key + ", keyError: " + keyError + ", err value: ", controlErrors[keyError]);
                });
            }
        });
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    /**
     *
     * @param item1 CompareWith
     * @param item2
     */
    byValue(item1, item2) {
        if (!item2) {
            return false;
        }
        return item1.value == item2.value;
    }

    displayDropdownCss(field) {
        return {
            "dropdown-error": field.invalid
        };
    }

    toggleDropdown(name) {
        this[name] = !this[name];
    }

    dropdownPlaceholderText(moduleName, items) {
        //console.log("items :", items);

        let toggle = moduleName["toggle"];
        let field = moduleName["field"];
        let field2 = moduleName["field2"];

        if (items.length == 1) {
            if (typeof field2 != "undefined") {
                this[toggle] = '<img src="' + items[0][field2] + '" height="25px" /> ' + items[0][field];
            } else {
                if (items[0]) {
                    this[toggle] = items[0][field];
                }
            }
        } else {
            if (toggle == "messagestockcritiqueDropdownPlaceholder") {
                this[toggle] = "Stock critique";
            } else if (toggle == "messagestockDropdownPlaceholder") {
                this[toggle] = "En stock";
            } else if (toggle == "messagehorsstockDropdownPlaceholder") {
                this[toggle] = "Hors stock";
            } else {
                this[toggle] = items.length + " selectionnés";
            }
        }
    }

    // clearFormArray = (formArray:FormArray) =>  {
    //     for (let i = formArray.length-1; i >= 0; i--) {
    //         formArray.removeAt(i);
    //       }
    //   }

    onDropdownSelected($event) {
        let module = this.modules.filter(module => module.name == $event.module);
        let control = <FormArray>this.optionPanierForm.controls[module[0]["props"]];
        control.removeAt(0);

        //console.log($event, '--> pushToArray -->', control);

        if ($event.ids.length == 0) {
            //formArray.setValue([{id:""}]);
        } else {
            $event.ids.forEach(element => {
                if (module["required"]) {
                    control.push(this.fb.group({ id: [element, Validators.required] }, Validators.required));
                } else {
                    control.push(this.fb.group({ id: [element, Validators.required] }));
                }
            });
        }

        if ("tva" == module[0].name) {
            this.produit["groupeRegleTaxe"] = $event.items[0];
            this.finalPrice(this.produit["prixHt"]);
        }

        let dropName = module[0]["dropdown"];
        this.dropdownPlaceholderText(module[0], $event.items);
        this[dropName] = false;
    }

    onDropdownDeleted($event) {
        //console.log('deleted', $event);
        let module = this.modules.filter(module => module.name == $event.module);
        let formArray = <FormArray>this.optionPanierForm.controls[module[0]["props"]];
        let valuesToKeep = [];

        for (let i = 0; i < formArray.value.length; i++) {
            if (formArray.value[i].id == $event.id.id) {
                formArray.removeAt(i);
            } else {
                //    console.log(formArray.value[i].id , '!=', $event.id.id);
                valuesToKeep.push(formArray.value[i]);
            }
        }

        if (module[0].name == "tva") {
            this.tvaApi.apiMagasinsSlugGroupesGrtRegleTaxeDelete(this.store.shopId.toString(), $event.id.id).subscribe(e => {
                // console.log('valuesToKeep', valuesToKeep);
                this.dropdownPlaceholderText(module[0], valuesToKeep);
            });
        }
    }

    calculateTtc(e) {
        this.produit["prixHt"] = e.target.value;
        this.finalPrice(e.target.value);
        this.optionPanierForm.patchValue({ prixHt: this.produit["prixHt"] });
    }

    calculateHt(e) {

        let txTva = 1;
        if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
            txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
        }

        let px = e.target.value.toString().replace(",", ".");
        this.originalPrice = e.target.value;
        this.produit["prixHt"] = (px / txTva)
            .toFixed(6)
            .toString()
            .replace(".", ",");
        this.optionPanierForm.patchValue({ prixHt: this.produit["prixHt"] });
        //  console.log('prixHt utilisé :',  ( px  /  txTva ) );
        //  console.log('txTva utilisé :', txTva);
        this.finalPrice(null);
    }

    finalPrice(price) {

        let txTva = 0;
        if (typeof this.produit["groupeRegleTaxe"] == "undefined") {
            txTva = 1;
        } else {
            txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
        }

        let prix;
        if (!price) {
            prix = this.produit["prixHt"].toString().replace(",", ".");
        } else {
            prix = price.toString().replace(",", ".");
        }

        this.originalPrice = (parseFloat(prix) * txTva)
            .toFixed(2)
            .toString()
            .replace(".", ",");
        //console.log('TTC PRICE :', this.originalPrice);
    }

    createForm(d) {

        let tva = "";
        if (d["groupeRegleTaxe"]) {
            tva = d["groupeRegleTaxe"].id;
            this.selectedItems.tva = [d["groupeRegleTaxe"]];
            this.dropdownPlaceholderText(this.modules[0], [d["groupeRegleTaxe"]]);
        } else {
            tva = this.tvas[0].id;
            this.produit['groupeRegleTaxe'] = this.tvas[0];
            this.selectedItems.tva = this.tvas[0];
            this.dropdownPlaceholderText(this.modules[0], [this.tvas[0]]);
        }

        this.optionPanierForm = this.fb.group({
            titre: [d["titre"], [Validators.required]],
            freeAfter: [d["freeAfter"], [Validators.min(0), Validators.pattern(this.pricePattern), Validators.required]],
            prixHt: [d["prixHt"].toString().replace(".", ","), [Validators.required, Validators.min(0), Validators.pattern(this.pricePattern)]],
            imageUrl: [d["imageUrl"], [Validators.required]],
            description: [d["description"], [Validators.required]]
        });

        if (!this.isAE) {
            this.optionPanierForm.addControl(
                "groupeRegleTaxe",
                this.fb.array([this.fb.group({ id: [tva, [Validators.required]] })], Validators.required)
            );
        }
        //categorie
    }

    ngOnInit() {
        this.tvaApi.apiMagasinsSlugGroupeRegleTaxesGet(this.store.shopId.toString()).subscribe(grt => {
            //console.log("GRTs : ", grt);
            this.tvas = grt;
            this.isTvaLoaded = true;

            this.preferenceApi.apiMagasinsSlugPreferenceGeneralesGet(this.store.shopId.toString()).subscribe(r => {
                this.isAE = !r.tvaRecuperable;
                this.devise = r.devise.sign;

                if (this.isAE) {
                    this.showHt = false;
                }

                this.Id = this.route.snapshot.params.id;
                if (this.route.snapshot.params.id) {
                    this.optionPanierApi
                        .apiMagasinsSlugOptionsProduitPanierGet(this.store.shopId.toString(), this.route.snapshot.params.id)
                        .subscribe(e => {
                            this.produit = e;

                            if (this.isAE) {
                                this.produit["groupeRegleTaxe"] = {
                                    libelle: "Groupe 20%",
                                    active: true,
                                    tempTaux: 0
                                };
                            }


                            if (typeof e["groupeRegleTaxe"] != "undefined") {
                                this.selectedItems.tva = [e["groupeRegleTaxe"]];
                            }

                            this.isProduitLoaded = true;
                            this.createForm(e);
                            this.finalPrice(null);
                        });
                } else {
                    this.produit = { ...this.fakeproduit };

                    if (this.isAE) {
                        this.produit["groupeRegleTaxe"] = {
                            libelle: "Groupe 20%",
                            active: true,
                            tempTaux: 0
                        };
                    }

                    this.createForm(this.produit);
                    //this.finalPrice(null);
                    this.isProduitLoaded = true;
                }
            });
        });
    }
}



