import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { StoreService } from "../../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { GrtService } from "../../../../api-shop";

@Component({
    selector: "app-shop-tva-form",
    templateUrl: "./tva-form.component.html",
    styleUrls: ["./tva-form.component.scss"]
})

export class TvaFormComponent implements OnInit {

    myForm: FormGroup;
    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() edited: EventEmitter<any> = new EventEmitter();
    @Input() tva;
    pricePattern = /^\d+(,\d{1,6})?$/;

    constructor(public store: StoreService, private fb: FormBuilder, private apiTva: GrtService) {}

    ngOnInit() {
        this.createForm();
    }

    displayFieldCss(form, field: string) {
        return {
          'we-is-invalid': ((!form.get(field).valid)),
        };
      }

    createForm(){

        let libelle = "";
        let tempTaux = "";


        if(this.tva.id){
               libelle = this.tva.libelle;
               tempTaux = this.tva.tempTaux;
        }



        this.myForm = this.fb.group({
            libelle: [libelle,  [
                Validators.required,
                Validators.minLength(2),
              ]],
            tempTaux: [tempTaux,  [
                Validators.required,
                Validators.min(0),
                Validators.max(100),
                Validators.pattern(this.pricePattern)
              ]],
            //description: ['', Validators.required],
          });
    }


    async submitHandler() {
        let formValue = this.myForm.value;
        let id = null;
        if(this.tva.id){
            id = this.tva.id;

             this.apiTva.apiMagasinsSlugGroupesGrtRegleTaxePut({libelle: formValue.libelle , tempTaux: formValue.tempTaux }, this.store.shopId.toString(), id).subscribe(e=>{
                 this.edited.emit({id:  id, libelle: formValue.libelle , tempTaux: formValue.tempTaux, created: ""  });
             });

        } else {
             this.apiTva.apiMagasinsSlugGroupesReglesTaxesPost({libelle: formValue.libelle , tempTaux: formValue.tempTaux }, this.store.shopId.toString()).subscribe(e=>{
                 this.added.emit({id: e.id, libelle: formValue.libelle , tempTaux: formValue.tempTaux , created : "" });
             });

        }

        this.myForm.reset();
        this.createForm();
    }

}



