import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { FormGroup, Validators, FormBuilder } from "@angular/forms";
import { StoreService } from "../../../../shared/services/store.service";
import { AttributService, AttributValeurService } from "../../../../api-shop";
import { ShopService } from "../../../services/shop.service";

@Component({
    selector: "app-shop-declinaison-builder",
    templateUrl: "./declinaison-builder.component.html",
    styleUrls: ["./declinaison-builder.component.scss"]
})
export class DeclinaisonBuilderComponent implements OnInit {
    constructor(
        public store: StoreService,
        private attributApi: AttributService,
        private attributValeurApi: AttributValeurService,
        private shopService: ShopService
    ) {}

    // Form state
    loading = false;
    success = false;
    addMore = false;
    step = 1;
    isValid = false;
    maxCategories = 10;
    articleCategories = [];
    editMode = false;
    editItem = null;
    editObj = null;
    newName = "";

    @Input() attributs;
    @Input() attributsFiltered;
    @Input() existingVariations;
    @Input() variants;

    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() reset: EventEmitter<any> = new EventEmitter();

    selectedItems = [];
    filteredAttributs = [];
    selectedV = [];
    items = [];
    combinaison = [];

    modules = [
        { name: "tva", field: "libelle" },
        { name: "categorie", field: "title" },
        { name: "fabricant", field: "nom", field2: "logo" },
        { name: "fournisseur", field: "nom" },
        { name: "tag", field: "nom" },
        { name: "messagestock", field: "message", field2: "logo" },
        { name: "messagestockcritique", field: "message", field2: "logo" },
        { name: "messagehorsstock", field: "message", field2: "logo" },
        { name: "transporteur", field: "nom", field2: "logo" }
    ];

    stepIn(s) {
        if (s == "1") {
            this.isValid = false;
            this.filteredAttributs = this.attributs;
            //this.selectedItems = [];
            this.existingVariations = [];
            this.selectedV = [];
            if (this.step == 2) {
                this.deleteCombinaison();
            }

            this.step = 1;
            return false;
        }

        if (s == "2" && this.selectedItems.length == 0) {
            return false;
        }

        let tempAttr = [];
        for (let i = 0; i < this.attributs.length; i++) {
            let index = this.selectedItems.indexOf(this.attributs[i].id);
            if (index > -1) {
                tempAttr.push(this.attributs[i]);
            }
        }

        // console.log('attr length :', tempAttr.length);
        // for(let b = 0; b < tempAttr.length; b ++){
        //     this.combinaison[b] = "";
        // }

        this.combinaison = new Array(tempAttr.length);
        this.editMode = false;
        this.editItem = 0;
        this.editObj = null;
        this.filteredAttributs = [...tempAttr];
        console.log(
            "TODO SAVE ORDER filtered attributs :",
            this.filteredAttributs
        );
        this.step = s;
    }

    onAdded(e) {}

    onAddNewAttribut() {
        let newopt = {
            //id: new Date().getTime(),
            nom: "Nouvelle Option",
            attributValeurs: [
                {
                    //id: new Date().getTime() + 1,
                    libelle: "valeur 1",
                    active: true,
                    ordre: 0
                },
                {
                    ///id: new Date().getTime() + 2,
                    libelle: "valeur 2",
                    active: true,
                    ordre: 0
                }
            ]
        };

        this.attributApi
            .apiMagasinsSlugAttributsPost(newopt, this.store.shopId.toString())
            .subscribe(e => {
                this.attributs.push(e);
                this.filteredAttributs = this.attributs;
            });
    }

    ngOnInit() {
        //console.log("variations existantes :", this.existingVariations);
        console.log(
            "TODO: LIMITER NB DECLIS DE PRODUIT",
            this.attributsFiltered
        );

        if (this.attributsFiltered.length == 0) {
            //this.filteredAttributs = [...this.attributs];
            this.filteredAttributs = this.attributs;
        } else {
            this.filteredAttributs = [...this.attributsFiltered];
            this.step = 2;
            this.combinaison = new Array(this.filteredAttributs.length);
        }
    }

    isSelected(item) {
        let index = this.selectedItems.indexOf(item.id);
        if (index == -1) {
            return null;
        } else {
            return "checked";
        }
    }

    unSelectedItem() {
        this.selectedItems = [];
    }

    //     isUnSelected(i,item){
    //         if(this.combinaison[i] == item.id){
    //             return {background : "#447fff", color: "white"};
    //         }
    //         return null;
    //    }

    isValSelected(i, item) {
        if (this.combinaison[i] == item.id) {
            return { background: "#447fff", color: "white" };
        }
        return null;
    }

    /** ARRAY DE INT ONLy */
    arraysEqual(a1, a2) {
        /* WARNING: arrays must not contain {objects} or behavior may be undefined */
        return JSON.stringify(a1) == JSON.stringify(a2);
    }

    onValeurAdded(e) {
        // console.log("added :", e, this.editObj);
        this.attributValeurApi
            .apiMagasinsAttributAttributsSlugValeursPost(
                { libelle: e.libelle, ordre: 0 },
                this.editItem,
                this.store.shopId.toString()
            )
            .subscribe(r => {
                console.log("bdd av :", r);
                this.editObj.attributValeurs[
                    this.editObj.attributValeurs.length - 1
                ] = r;
            });
    }

    onValeurRemoved(e) {

        //alert("afficher un message si valeur et relier à d'autres declis");
        //console.log("removed :", e);

        this.attributValeurApi
            .apiMagasinsSlugAttributsAttributValeursAttributValeurDelete(
                this.store.shopId.toString(),
                this.editItem,
                e.id
            )
            .subscribe(r => {
                console.log("rm bdd av :", r);
                if(e['status']){
             
                }else{
                    alert("Cette variante est reliée à d'autres produit et ne peut etre supprimée.");
                }
            });
    }



    onAttributDelete(item) {

        this.attributApi
            .apiMagasinsSlugAttributsAttributDelete(
                this.store.shopId.toString(),
                item.id
            )
            .subscribe(e => {
                if(e['status']){
                    console.log("attribut bdd deleted :", e);
                    for (let i = 0; i < this.filteredAttributs.length; i++) {
                        if (this.filteredAttributs[i].id == item.id) {
                            this.filteredAttributs.splice(i, 1);
                        }
                    }
                    for (let b = 0; b < this.attributs.length; b++) {
                        if (this.attributs[b].id == item.id) {
                            this.attributs.splice(b, 1);
                        }
                    }
                }else{
                    alert("Cet attribut est relié à d'autres produit et ne peut etre supprimé.");
                }

            });
    }

    onAttributEdit(item) {
        // console.log("attribut edit :", item);
        this.newName = item.nom;
        this.editItem = item.id;
        this.editObj = item;
        this.editMode = true;
    }

    updateName(e) {
        this.newName = e.target.value;
    }

    onAttributSave(item) {
        item.nom = this.newName;
        let itemClone = { ...item };
        // console.log("attribut save :", itemClone);

        let attrClone = [];

        for (let b = 0; b < itemClone.attributValeurs.length; b++) {
            attrClone.push({
                libelle: itemClone.attributValeurs[b].libelle,
                active: true,
                ordre: 0
            });
        }

        itemClone.attributValeurs = attrClone;
        delete itemClone["id"];

        this.attributApi
            .apiMagasinsSlugAttributsAttributPut(
                itemClone,
                this.store.shopId.toString(),
                this.editItem
            )
            .subscribe(e => {
                this.editMode = false;
                this.editItem = 0;
                this.editObj = null;
                this.newName = "";
            });
    }

    deleteCombinaison() {
        // console.log("delete de toutes les variantes existantes", this);
        this.reset.emit();
    }

    addCombinaison() {
        if (this.isValid) {
            //console.log(this.existingVariations.length);
            if (this.existingVariations.length < 100) {
                //console.log('add combi', this.combinaison);
                this.existingVariations.push([...this.combinaison]);
                this.isValid = false;
                this.added.emit({
                    attributs: this.filteredAttributs,
                    combinaison: this.combinaison
                });
            } else {
                alert(
                    "Limite maximum de 100 déclinaisons par produit atteinte."
                );
            }
        }
    }

    onSelectedValeur(i, item) {
        let wanted = [...this.combinaison];
        wanted[i] = item.id;

        this.combinaison[i] = item.id;

        // if(this.combinaison.length == this.existingVariations.length){
        for (let p = 0; p < this.existingVariations.length; p++) {
            if (this.arraysEqual(wanted, this.existingVariations[p])) {
                this.isValid = false;
                return true;
            }
        }

        //this.combinaison[i] = item.id;

        // }

        for (let e = 0; e < this.combinaison.length; e++) {
            if (isNaN(this.combinaison[e])) {
                // console.log('Combi Index "Is NOt a Numeric" va generer un bug ', this.combinaison);
                if (!this.combinaison[e]) {
                    return false;
                }
            }
        }

        this.isValid = true;
        // console.log("tester chaque dim et autoriser le btn ", this.combinaison);
    }

    onSelectedItem(item) {
        let index = this.selectedItems.indexOf(item.id);
        if (index == -1) {
            this.selectedItems.push(item.id);
        } else {
            this.selectedItems.splice(index, 1);
        }
        // console.log("selected :", this.selectedItems);
    }
}



