import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from "@angular/core";
import { StoreService } from "../../../shared/services/store.service";
import { FormGroup, Validators, FormBuilder } from "@angular/forms";
import { ModalComponent } from "../../../shared/components/modal/modal.component";

@Component({
    selector: "app-shop-dropdown",
    templateUrl: "./shop-dropdown.component.html",
    styleUrls: ["./shop-dropdown.component.scss"]
})
export class ShopDropdownComponent implements OnInit {
    constructor(public store: StoreService) {}

    @ViewChild("modalDelete") modalDelete: ModalComponent;

    // Form state
    loading = false;
    success = false;
    addMore = false;
    @Input() showDropdown;
    @Input() moduleName;
    @Input() selected;
    @Input() multiple;
    @Input() onOpen;
    @Input() listingItems;
    @Input() mandatory;
    @Input() defaultMessage;
    @Input() isMulti;
    @Input() isLoaded;
    editableObj = {};

    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() deleted: EventEmitter<any> = new EventEmitter();
    radioName = "";

    selectedItems = [];
    items = [];
    modules = [
        { name: "tva", field: "libelle", isEditable: true },
        { name: "categorie", field: "title", isEditable: true },
        { name: "fabricant", field: "nom", field2: "logo", isEditable: true },
        { name: "produit", field: "titre", field2: "imageUrl", isEditable: false },
        { name: "fournisseur", field: "nom", isEditable: true },
        { name: "tag", field: "nom", isEditable: true },
        { name: "messagestock", field: "message", field2: "logo", isEditable: true },
        { name: "messagestockcritique", field: "message", field2: "logo", isEditable: true },
        { name: "messagehorsstock", field: "message", field2: "logo", isEditable: true },
        { name: "transporteur", field: "nom", field2: "logo", isEditable: false },
        { name: "groupe", field: "nom", isEditable: true },
        { name: "groupew", field: "nom", isEditable: false }
    ];

    itemToDelete = null;

    onEdit(e) {
        this.addMore = true;
        this.editableObj = e;
    }

    onPreDelete(e) {
        this.itemToDelete = e;
        this.modalDelete.open();
    }

    onDelete() {
        for (let elmt = 0; elmt < this.selectedItems.length; elmt++) {
            if (this.itemToDelete.id == this.selectedItems[elmt]) {
                this.selectedItems.splice(elmt, 1);
            }
        }

        for (let elmt = 0; elmt < this.listingItems.length; elmt++) {
            if (this.itemToDelete.id == this.listingItems[elmt].id) {
                this.listingItems.splice(elmt, 1);
            }
        }

        //this.items = [...this.listingItems];

        this.modalDelete.close();
        this.deleted.emit({ id: this.itemToDelete, module: this.moduleName });
    }

    onEdited(e) {
        // console.log(e);
        this.addMore = false;

        for (let elmt = 0; elmt < this.listingItems.length; elmt++) {
            if (e.id == this.listingItems[elmt].id) {
                this.listingItems.splice(elmt, 1);
            }
        }

        this.listingItems.push(e);
        this.editableObj = {};
        //  this.selectedItems.push(e.id);
        //this.items = [...this.listingItems];
    }

    onAdded(e) {
        // console.log(e);
        this.addMore = false;
        this.listingItems.push(e);

        if (this.isMulti == "true") {
            this.selectedItems.push(e.id);
        } else {
            this.selectedItems = [e.id];
        }

        //this.items = [...this.listingItems];
    }

    isItemEditable() {
        let module = this.modules.filter(mod => mod.name == this.moduleName);
        return module[0].isEditable;
    }

    ngOnInit() {
        this.radioName = this.moduleName + " " + new Date().getTime();
        if (typeof this.selected != "undefined") {
            if (Array.isArray(this.selected)) {
                for (let i = 0; i < this.selected.length; i++) {
                    if (typeof this.selected[i] != "undefined") {
                        //console.log(this.selected[i]);
                        if (this.selected[i]) {
                            this.selectedItems.push(this.selected[i].id);
                        }
                    }
                }
            }
        }

        //this.items = [...this.listingItems];
    }

    onFilterItems(evt) {
        let module = this.modules.filter(mod => mod.name == this.moduleName);
        if (evt.target.value == "") {
            this.items = [...this.listingItems];
        } else {
            this.items = [...this.listingItems].filter(
                word => word[module[0]["field"]].toLocaleLowerCase().indexOf(evt.target.value.toLocaleLowerCase()) > -1
            );
        }
    }

    isSelected(item) {
        let index = this.selectedItems.indexOf(item.id);
        if (index == -1) {
            return null;
        } else {
            return "checked";
        }
    }

    unSelectedItem() {
        this.selectedItems = [];
    }

    onSelectedItem(item) {
        // console.log(this.moduleName, 'is Multi', this.isMulti );

        if (this.isMulti == "true") {
            let index = this.selectedItems.indexOf(item.id);

            if (index == -1) {
                this.selectedItems.push(item.id);
            } else {
                this.selectedItems.splice(index, 1);
            }
        } else {
            this.selectedItems = [item.id];
        }

        //console.log('selected :', this.selectedItems);
    }

    onAddMore() {
        //console.log("addMore");
        this.addMore = true;
    }

    onValidateChoice() {
        let fullTextItems = [];

        for (let i = 0; i < this.listingItems.length; i++) {
            let index = this.selectedItems.indexOf(this.listingItems[i].id);

            if (index > -1) {
                fullTextItems.push(this.listingItems[i]);
            }
        }

        this.added.emit({ ids: this.selectedItems, items: fullTextItems, module: this.moduleName });
        //this.selectedItems = [];
    }
}



