import {
    Component,
    OnInit,
    Input,
    ViewChild,
    ChangeDetectorRef,
    OnChanges,
    SimpleChanges,
    EventEmitter,
    Output
} from "@angular/core";
import { ModalComponent } from "../../../../../shared/components/modal/modal.component";
import { CommandeService } from "../../../../../api-paiement";
import { StoreService } from "../../../../../shared/services/store.service";

@Component({
    selector: "app-form-list-ceadp",
    templateUrl: "./form-list-ceadp.component.html",
    styleUrls: ["./form-list-ceadp.component.scss"]
})
export class FormListCeadpComponent implements OnInit {
    @Input() table: { head: string[]; body: any[] };
    @Output() statusChange: EventEmitter<any> = new EventEmitter();

    @ViewChild(ModalComponent) modal: ModalComponent;

    nbStock = 0;
    productSelected = null;
    showModal = false;

    statutsPaiement = [
        { id: 1, label: "Paiement en attente" },
        { id: 2, label: "Paiement accepté" },
        { id: 3, label: "Paiement refusé" },
        { id: 4, label: "Paiement remboursé" }
    ];

    wantChangeStatus = {};

    selectedStatut = null;
    selectedItem = null;
    pendingRequest = false;

    constructor(
        private cd: ChangeDetectorRef,
        private commandeApi: CommandeService,
        private store: StoreService
    ) {}

    ngOnInit() {}

    clickOnBtnUpdate(row) {
        this.selectedItem = row;
        this.showModal = true;
        this.cd.detectChanges();
        this.modal.open();
    }

    handleChangeState($event, row) {
        row.newIdStatutPaiement = $event.target.value;
    }

    update() {
        if (
            this.pendingRequest ||
            !this.store.curSite.uids.find(u => u.nom === "paiement")
        ) {
            return;
        }
        this.pendingRequest = true;
        this.commandeApi
            .apiMagasinsSlugCommandesIdEtatsStateIdPaiementsPost(
                String(
                    this.store.curSite.uids.find(u => u.nom === "paiement")
                        .distant_id
                ),
                this.selectedItem.id,
                this.selectedItem.newIdStatutPaiement
            )
            .subscribe(() => {
                this.table.body = this.table.body.filter(
                    row => row.id !== this.selectedItem.id
                );
                this.showModal = false;
                this.selectedItem = null;
                this.pendingRequest = false;
                this.modal.close();
                this.cd.detectChanges();
                this.statusChange.emit();
            });
    }
}



