    ngOnInit() {


        //console.log("TODO : Charger le bon type de facture TTC ou HT +TVA");
        //console.log("TODO : LOGO A LOADER");
        //console.log("TODO : POSTER vers  API Paiement le compteur de facture");

        this.designsMApi
            .apiSitesSlugDesignModuleGet(this.store.curSite.id.toString())
            .subscribe(e => {
                this.designM = e;
                this.cfgSite = JSON.parse(e.config);

                let paiementModule = this.store.getUIDByName("paiement");
                if (paiementModule) {

                    //on doit recuperer aussi la valeur du compteur de facture  sur l'API
                    this.paiementApi.apiMagasinsMpbGet(paiementModule.distant_id.toString()).subscribe(p => {

                        this.props.numLastFacture = p['compteur_facturation'];
                        this.initProps();
                        this.scriptService
                            .loader("themeBSUser", "FactureRcpt")
                            .then(() => {
                                this.rcpt = window["FactureRcpt"].default;
                                this.scriptsLoaded = true;
                            });
                    })
                }



            });
    }



