import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { StoreService } from "../../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MessageDispoService } from "../../../../api-shop";

@Component({
    selector: "app-shop-messagedispo-form",
    templateUrl: "./messagedispo-form.component.html",
    styleUrls: ["./messagedispo-form.component.scss"]
})
export class MessageDispoFormComponent implements OnInit {
    myForm: FormGroup;
    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() edited: EventEmitter<any> = new EventEmitter();
    @Input() reference;
    @Input() messagedispo;

    constructor(public store: StoreService, private fb: FormBuilder, private apiMessageDispo: MessageDispoService) {}

    ngOnInit() {
        this.createForm();
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    createForm() {
        let message = "";
        let logo = "";

        if (this.messagedispo.id) {
            message = this.messagedispo.message;
            logo = this.messagedispo.logo;
        }

        this.myForm = this.fb.group({
            message: [message, [Validators.required, Validators.minLength(3), Validators.maxLength(250)]],
            logo: [logo, Validators.required],
            reference: [this.reference, [Validators.min(0), Validators.max(2)]]
        });
    }

    async submitHandler() {
        let formValue = this.myForm.value;
        let id = null;
        if (this.messagedispo.id) {
            id = this.messagedispo.id;
            this.apiMessageDispo
                .apiMagasinsSlugMessagesMessageDispoDisponibiliteProduitPut(
                    { message: formValue.message, logo: formValue.logo, reference: formValue.reference },
                    this.store.shopId.toString(),
                    id
                )
                .subscribe(e => {
                    this.edited.emit({
                        id: id,
                        message: formValue.message,
                        logo: formValue.logo,
                        reference: formValue.reference
                    });
                    this.myForm.reset();
                    this.createForm();
                });
        } else {
            this.apiMessageDispo
                .apiMagasinsSlugMessagesDisponibilitesProduitsPost(
                    { message: formValue.message, logo: formValue.logo, reference: formValue.reference },
                    this.store.shopId.toString()
                )
                .subscribe(e => {
                    this.added.emit({
                        id: e.id,
                        message: formValue.message,
                        logo: formValue.logo,
                        reference: formValue.reference
                    });
                    this.myForm.reset();
                    this.createForm();
                });
        }
    }
}



