import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { StoreService } from "../../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { FournisseurService } from "../../../../api-shop";

@Component({
    selector: "app-shop-fournisseur-form",
    templateUrl: "./fournisseur-form.component.html",
    styleUrls: ["./fournisseur-form.component.scss"]
})
export class FournisseurFormComponent implements OnInit {
    myForm: FormGroup;
    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() edited: EventEmitter<any> = new EventEmitter();
    @Input() fournisseur;

    constructor(public store: StoreService, private fb: FormBuilder, private apiFournisseur: FournisseurService) {}

    ngOnInit() {
        this.createForm();
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    createForm() {
        let nom = "";
        let description = "";

        if (this.fournisseur.id) {
            nom = this.fournisseur.nom;
            description = this.fournisseur.description;
        }

        this.myForm = this.fb.group({
            nom: [nom, [Validators.required, Validators.minLength(2)]],
            description: [description, [Validators.minLength(2), Validators.maxLength(1400)]]
        });
    }

    async submitHandler() {
        let formValue = this.myForm.value;
        let id = null;

        if (this.fournisseur.id) {
            id = this.fournisseur.id;
            this.apiFournisseur
                .apiMagasinsSlugFournisseursFournisseurPut(
                    { nom: formValue.nom, description: formValue.description },
                    this.store.shopId.toString(),
                    id
                )
                .subscribe(e => {
                    this.edited.emit({ id: id, nom: formValue.nom, description: formValue.description, logo: formValue.logo });
                    this.myForm.reset();
                    this.createForm();
                });
        } else {
            this.apiFournisseur
                .apiMagasinsSlugFournisseursPost({ nom: formValue.nom, description: formValue.description }, this.store.shopId.toString())
                .subscribe(e => {
                    this.added.emit({ id: e.id, nom: formValue.nom, description: formValue.description });
                    this.myForm.reset();
                    this.createForm();
                });
        }
    }
}



