import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { StoreService } from "../../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { TagService } from "../../../../api-shop";

@Component({
    selector: "app-shop-tag-form",
    templateUrl: "./tag-form.component.html",
    styleUrls: ["./tag-form.component.scss"]
})
export class TagFormComponent implements OnInit {
    myForm: FormGroup;
    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() edited: EventEmitter<any> = new EventEmitter();
    @Input() tag;

    constructor(public store: StoreService, private fb: FormBuilder, private apiTag: TagService) {}

    ngOnInit() {
        this.createForm();
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    createForm() {
        let nom = "";

        if (this.tag.id) {
            nom = this.tag.nom;
        }

        this.myForm = this.fb.group({
            nom: [nom, [Validators.required, Validators.minLength(2), Validators.maxLength(50)]]
            //description: ['', Validators.required],
        });
    }

    async submitHandler() {
        let formValue = this.myForm.value;
        let id = null;
        if (this.tag.id) {
            id = this.tag.id;
            this.apiTag.apiMagasinsSlugTagsTagPut({ nom: formValue.nom, ordre: 0 }, this.store.shopId.toString(), id).subscribe(e => {
                this.edited.emit({ id: id, nom: formValue.nom });
                this.myForm.reset();
                this.createForm();
            });
        } else {
            this.apiTag.apiMagasinsSlugTagsPost({ nom: formValue.nom, ordre: 0 }, this.store.shopId.toString()).subscribe(e => {
                this.added.emit({ id: e.id, nom: formValue.nom });
                this.myForm.reset();
                this.createForm();
            });
        }
    }
}



