import {
  Component,
  OnInit,
  ViewChild,
  EventEmitter,
  Output,
  ChangeDetectorRef,
  OnDestroy
} from "@angular/core";
import { StoreService } from "../../../shared/services/store.service";
import { ScriptService } from "../../../shared/services/script.service";
import { ActivatedRoute, Router } from "@angular/router";
import { ContainerComponent, DraggableComponent } from 'ngx-smooth-dnd';
import { CursusItemType, CursusService, FormationsService } from "../../../api-formation";
import { ModuleService } from "../../../global/services/module.service";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { PagesService, PageType, SitesService } from "../../../api";
import { ProduitService } from "../../../api-shop";
import { scrollTo } from "../../../shared/utils/utils";



const cardColors = ['azure', 'beige', 'bisque', 'blanchedalmond', 'burlywood', 'cornsilk', 'gainsboro', 'ghostwhite', 'ivory', 'khaki'];
const pickColor = () => {
  const rand = Math.floor((Math.random() * 10));
  return cardColors[rand];
};
const applyDrag = (arr, dragResult) => {
  const { removedIndex, addedIndex, payload } = dragResult;
  if (removedIndex === null && addedIndex === null) return arr;

  const result = [...arr];
  let itemToAdd = payload;

  if (removedIndex !== null) {
    itemToAdd = result.splice(removedIndex, 1)[0];
  }

  if (addedIndex !== null) {
    result.splice(addedIndex, 0, itemToAdd);
  }

  return result;
};




@Component({
  selector: 'app-cursus-formation',
  templateUrl: './cursus-formation.component.html',
  styleUrls: ['./cursus-formation.component.scss']
})
export class CursusFormationComponent implements OnInit, OnDestroy {



  @ViewChild("modalDelete") modalDelete: ModalComponent;

  isCursusLoaded = false;
  currentItemInEdit = { metadata: { id: 0 } };
  currentPayload = {};
  itemToDelete = { item: {}, chapitre: {}, index: 0 };
  chapitreToDelete = { chapitre: {}, index: 0 };
  treeRootId = 0;
  productId = 0;
  isLoading = false;
  totalPages = 0;
  maxPages = 100;
  formationId = "0"; // info dans le produit
  produit = { titre: "" };

  scene = {
    type: 'container',
    props: {
      orientation: 'vertical'
    },
    children: []
  }

  generateItems = (children, creator) => {
    const result = [];

    /*   for (let i = 0; i < count; i++) {
        result.push(creator(i));
      } */
    let i = 0;
    if (Array.isArray(children)) {
      children.forEach(element => {
        if (element.is_cours) {
          this.totalPages++;
        }
        result.push(creator(i, element));
        i++;
      });
    }

    return result;
  };

  constructor(
    public store: StoreService,
    private scriptService: ScriptService,
    private route: ActivatedRoute,
    private moduleService: ModuleService,
    private formationApi: FormationsService,
    private cursusApi: CursusService,
    private pageApi: PagesService,
    private siteApi: SitesService,
    private produitApi: ProduitService,
    private router: Router) { }


  ngOnInit(): void {

    this.isCursusLoaded = true;
    this.isLoading = false;
    this.moduleService.activateModule("formation").then(() => {

      if (this.route.snapshot.params.id) {
        this.productId = this.route.snapshot.params.id;
        this.produitApi
          .apiMagasinsSlugProduitsProduitGet(
            this.store.shopId.toString(),
            this.route.snapshot.params.id
          )
          .subscribe(a => {
            this.produit = a;
            this.formationId = a['formationId'];
            this.cursusApi.publicFormationsSlugCursusitemsGet(this.formationId).subscribe((e) => {
              if (e["items"] && e["items"].length == 1) {
                this.buildTreeview(e["items"]);
              }
            });
          });
      }

    });
  }


  //Rééordonne les elements
  compareOrdre(a, b) {
    if (a.metadata.ordre < b.metadata.ordre) {
      return -1;
    }
    if (a.metadata.ordre > b.metadata.ordre) {
      return 1;
    }
    return 0;
  }


  buildTreeview(data) {
    let root = data.find((e) => {
      return ((e.is_cours == false) && (e.is_chapitre == false))
    });

    this.treeRootId = root.id;
    this.scene = {
      type: 'container',
      props: {
        orientation: 'vertical'
      },
      children: this.generateItems(root.children, (i, e) => ({
        id: e.id,
        type: 'container',
        metadata: e,
        props: {
          orientation: 'vertical',
          className: 'card-container'
        },
        children: this.generateItems(e.children, (j, e) => ({
          type: 'draggable',
          id: e.id,
          metadata: e,
          props: {
            className: 'card',
            style: { backgroundColor: pickColor() }
          },
        })).sort(this.compareOrdre)
      })).sort(this.compareOrdre)
    };
    this.isLoading = false;

  }

  ngOnDestroy(): void { }

  onColumnDrop(dropResult) {
    const scene = Object.assign({}, this.scene);
    scene.children = applyDrag(scene.children, dropResult);
    let newOrder = []; // {id : , rank: }
    let i = 0;
    scene.children.forEach(element => {
      newOrder.push({ id: element.id, rank: i });
      i++;
    });

    this.saveOrder(newOrder, () => { this.scene = scene; this.isLoading = false; })
  }

  onCardDrop(columnId, dropResult) {
    if (dropResult.removedIndex !== null || dropResult.addedIndex !== null) {
      const scene = Object.assign({}, this.scene);
      const column = scene.children.filter(p => p.id === columnId)[0];
      const columnIndex = scene.children.indexOf(column);

      const newColumn = Object.assign({}, column);
      newColumn.children = applyDrag(newColumn.children, dropResult);
      scene.children.splice(columnIndex, 1, newColumn);


      let newOrder = []; // {id : , rank: }
      let i = 0;
      newColumn.children.forEach(element => {
        newOrder.push({ id: element.id, rank: i });
        i++;
      });


      let cursusItem: CursusItemType = {
        title: this.currentPayload['metadata'].title,
        isFree: this.currentPayload['metadata'].isFree,
        parent: columnId
      }

      this.isLoading = true;
      //update le parent si la card a été déplace dans un autre chapitre
      this.cursusApi.apiFormationsSlugCursusitemsCursusPatchPut(cursusItem, this.formationId, this.currentPayload['metadata'].id).subscribe(e => {
        this.saveOrder(newOrder, () => { this.scene = scene; this.isLoading = false; })
      });
    }
  }


  resetCurrentItem() {
    this.currentItemInEdit = { metadata: { id: 0 } };
  }

  //activation du mode edit de texte
  edit(item) {
    if (item == this.currentItemInEdit) {
      this.resetCurrentItem();
    } else {
      this.currentItemInEdit = item;
    }

  }

  /**
   * Page/Cours offerte 
   * @param item 
   */
  isGiven(item) {
    if (this.isLoading) {
      return;
    }
    //alert("Maj les droits de la page avant, SQL sur les containers pour mettre isFree");
    //console.log("isGiven:", item);
    item.metadata.isFree = !item.metadata.isFree;

    let cursusItem: CursusItemType = {
      title: item.metadata.title,
      isFree: item.metadata.isFree
    }

    this.isLoading = true;
    this.cursusApi.apiFormationsSlugCursusitemsCursusPatchPut(cursusItem, this.formationId, item.metadata.id).subscribe(e => {
      ///console.log("given saved:", item);
      this.isLoading = false;
    });
  }

  /**
   * 
   * @param item Page/Cours visible
   */
  setVisibility(item) {
    if (this.isLoading) {
      return;
    }
    item.metadata.visible = !item.metadata.visible;

    let cursusItem: CursusItemType = {
      title: item.metadata.title,
      duration: item.metadata.duration,
      visible: item.metadata.visible,
    }

    this.isLoading = true;
    this.cursusApi.apiFormationsSlugCursusitemsCursusPatchPut(cursusItem, this.formationId, item.metadata.id).subscribe(e => {
      //alert("Changer la visibility de la page aussi ???");
      //console.log("visible saved:", item);
      this.isLoading = false;
    });
    //bdd update
  }

  /**
   * Delete un cours puis page
   * Check si on a affaire a un cours ou un chapitre
   * @param item 
   */
  delete() {
    if (this.isLoading) {
      return;
    }
    this.isLoading = true;
    if (this.chapitreToDelete && (typeof this.chapitreToDelete.chapitre['metadata'] !== "undefined")) {
      this.deleteChapitre();
    } else {
      const { item, chapitre, index } = this.itemToDelete;

      let pageIdToDeleteId = item['metadata'].page_id > 0 ? item['metadata'].page_id : item['metadata'].pageId;
      this.cursusApi.apiFormationsSlugCursusitemsCursusDelete(this.formationId, item['metadata'].id).subscribe(e => {
        this.pageApi.apiPagesPageDelete(pageIdToDeleteId).subscribe(p => {
          const scene = Object.assign({}, this.scene);
          const column = scene.children.filter(p => p.id === chapitre['id'])[0];
          column.children.splice(index, 1);
          this.modalDelete.close();
          this.totalPages--;
          this.scene = scene;
          this.isLoading = false;
        })

      })
    }


  }

  /**
  *  AJoute un chapitre
  */
  addChapitre() {
    if (this.isLoading) {
      return;
    }


    let cursusItem: CursusItemType = {
      title: "Mon Nouveau chapitre",
      isCours: false,
      isChapitre: true,
      isFree: false,
      pageId: 0,
      duration: 0,
      visible: true,
      ordre: 1000,
      parent: this.treeRootId.toString()
    }

    this.isLoading = true;
    this.cursusApi.apiFormationsSlugCursusitemsPost(cursusItem, this.formationId).subscribe(e => {
      let newChapitre = {
        id: e.id,
        type: 'container',
        metadata: e,
        props: {
          orientation: 'vertical',
          className: 'card-container'
        },
        children: []
      };
      this.isLoading = false;

      const scene = Object.assign({}, this.scene);
      const column = scene.children.push(newChapitre);
      this.scene = scene;
      scrollTo(["card-container"], true);
    });
  }

  /**
 * Delete un chapitre puis page
 * @param item 
 */
  deleteChapitre() {
    const { chapitre, index } = this.chapitreToDelete;
    this.cursusApi.apiFormationsSlugCursusitemsCursusDelete(this.formationId, chapitre['metadata'].id).subscribe(e => {
      const scene = Object.assign({}, this.scene);
      scene.children.splice(index, 1);
      this.modalDelete.close();
      this.scene = scene;
      this.isLoading = false;
    })

  }

  deleteIntent(item, chapitre, index) {
    this.itemToDelete = { item, chapitre, index };
    this.modalDelete.open();
  }


  deleteChapitreIntent(chapitre, index) {
    this.chapitreToDelete = { chapitre, index };
    this.modalDelete.open();
  }


  /**
   * Retour vers la page dans l'éditeur
   * @param item 
   */
  goToPage(item) {
    const strMsg = JSON.stringify({
      goToModule: "formation",
      goToModuleUrl: "/editeur/pages/formation/" + item.metadata.page_id,
    });
    parent.postMessage(strMsg, "*");
  }


  /**
  * Créer un objet pageItem pour la page de cursus
  */
  createCursusPage(e) {

    const page: PageType = {
      nom: e.title,
      url: " formations/" + this.route.snapshot.params.id + "/cursus/" + e.id,
      editor: "10", // choper lediteur sur le site
      is_home: false,
      is_error: false,
      module: "5",
      module_resource: this.route.snapshot.params.id,
      module_page_type: "cursus",
      is_service: false
    };
    return page;
  }


  /**
   * Ajoute un cours et une Page
   */
  addCours(chapitre) {
    if (this.isLoading) {
      return;
    }

    let cursusItem: CursusItemType = {
      title: "Mon Nouveau cours " + (this.totalPages + 1),
      isCours: true,
      isChapitre: false,
      isFree: false,
      pageId: 0,
      duration: 0,
      visible: false,
      ordre: 1000,
      parent: chapitre.metadata.id
    }

    this.isLoading = true;

    //1er post crea 
    this.cursusApi.apiFormationsSlugCursusitemsPost(cursusItem, this.formationId).subscribe(e => {

      // on a besoin d'abord des infos du cursusItem pour les communiquer à l'APi Page
      let siteId = this.store.curSite.id;
      let pageItem = this.createCursusPage(e);
      this.siteApi.apiSitesSitePagesPost(pageItem, siteId.toString()).subscribe(p => {

        let cursusItemPatch: CursusItemType = {
          title: e.title,
          pageId: p.id,
        };

        //on maj le cursusItem avec le pageId
        this.cursusApi.apiFormationsSlugCursusitemsCursusPatchPut(cursusItemPatch, this.formationId, e.id.toString()).subscribe(cursusFinal => {

          let coursClone = { ...e };
          coursClone['page_id'] = p.id;

          let newCours = {
            type: 'draggable',
            id: "test",
            metadata: coursClone,
            props: {
              className: 'card',
              style: { backgroundColor: pickColor() }
            },
            data: "test"
          };

          const scene = Object.assign({}, this.scene);
          const column = scene.children.filter(p => p.id === chapitre.id)[0];
          const newColumn = Object.assign({}, column);
          newColumn.children.push(newCours);
          this.totalPages++;
          this.scene = scene;
          this.isLoading = false;

        });

      });
    });

  }

  dragStart(e) {
    this.currentPayload = e.payload;
    this.isLoading = true;
  }


  dragEnd(e) {
    // console.log("End Drag", e);
    //this.currentPayload = {};
  }

  /**
   * Save la treeview et ordre
   */
  saveOrder(newOrder, callback) {
    this.cursusApi.apiFormationsSlugCursusitemOrderPut(newOrder, this.formationId).subscribe(e => {
      callback();
    });
  }



  /**
   * Save le new name de l'item
   */
  saveCours(item, chapitre, index) {
    if (this.isLoading) {
      return;
    }

    let cursusItem: CursusItemType = {
      title: item.metadata.title,
      duration: item.metadata.duration,
      visible: item.metadata.visible,
      isCours: true

    }
    this.isLoading = true;
    this.cursusApi.apiFormationsSlugCursusitemsCursusPatchPut(cursusItem, this.formationId, item.metadata.id).subscribe(e => {
      const scene = Object.assign({}, this.scene);
      const column = scene.children.filter(p => p.id === chapitre.id)[0];
      column.children[index] = item;
      this.isLoading = false;
      this.scene = scene;
      this.resetCurrentItem();
    });

  }

  /**
  * Save le new name de l'item
  */
  saveChapitre(chapitre) {
    if (this.isLoading) {
      return;
    }

    let cursusItem: CursusItemType = {
      title: chapitre.metadata.title,
      duration: chapitre.metadata.duration,
      visible: chapitre.metadata.visible,
      isChapitre: chapitre.metadata.isChapitre

    }
    this.isLoading = true;
    this.cursusApi.apiFormationsSlugCursusitemsCursusPatchPut(cursusItem, this.formationId, chapitre.metadata.id).subscribe(e => {
      const scene = Object.assign({}, this.scene);
      let column = scene.children.filter(p => p.id === chapitre.id)[0];
      column = chapitre;
      this.isLoading = false;
      this.scene = scene;
      this.resetCurrentItem();
    })
  }

  /**
   * Item a t'il une page brouuillon non publiée
   */
  hasDraft() {

  }

  getCardPayload(columnId) {
    return (index) => {
      return this.scene.children.filter(p => p.id === columnId)[0].children[index];
    }
  }

  log(...params) {
    console.log(...params);
  }


}



