import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { StoreService } from '../../../../shared/services/store.service';
import { GroupeService } from '../../../../api-member';

@Component({
  selector: 'app-user-group-form',
  templateUrl: './user-group-form.component.html',
  styleUrls: ['./user-group-form.component.scss']
})

export class UserGroupFormComponent implements OnInit {

    myForm: FormGroup;
    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() edited: EventEmitter<any> = new EventEmitter();
    @Input() groupe;



    constructor(public store: StoreService, private fb: FormBuilder, private apiGroupe: GroupeService) {}

    ngOnInit() {
        // console.log("init categorie :", this.categorie);
        this.createForm();
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    createForm() {
        let title = "";
        // let description = "";

        if (this.groupe.id) {
            title = this.groupe.nom;
            // description = this.categorie.description;
        }

        this.myForm = this.fb.group({
            title: [title, [Validators.required, Validators.minLength(2), Validators.maxLength(150)]]
            // description: [description, Validators.required]
        });
    }

    async submitHandler() {
        let formValue = this.myForm.value;
        let id = null;
        if (this.groupe.id) {
            id = this.groupe.id;
            this.apiGroupe.apiMagasinsSlugGroupesGroupePut({ nom: formValue.title }, this.store.memberId.toString(), id).subscribe(e=>{
                this.edited.emit({ id: id, nom: formValue.title });
                this.myForm.reset();
                this.createForm();
            });

        } else {
            this.apiGroupe.apiMagasinsSlugGroupesPost({ nom: formValue.title }, this.store.memberId.toString()).subscribe(e=>{
                this.added.emit({ id: e.id, nom: formValue.title });
                this.myForm.reset();
                this.createForm();
            });
        }
    }
}



