import { Component, OnInit, Input, ViewChild, AfterViewInit, ElementRef, EventEmitter, Output } from "@angular/core";
import { StoreService } from "../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalComponent } from "../../../shared/components/modal/modal.component";

@Component({
    selector: "app-shop-itembar",
    templateUrl: "./itembar.component.html",
    styleUrls: ["./itembar.component.scss"]
})
export class ItemBarComponent implements OnInit {
    @Input() domain;
    @Input() isLoading;
    @Input() metaPage; //check page meta 
    @Input() isDuplicable;
    @Input() state;
    @Input() breadcrum = false;
    @Input() showBtnSave = true;
    @Output() onSave: EventEmitter<any> = new EventEmitter();
    @Output() onDelete: EventEmitter<any> = new EventEmitter();
    @Output() onDuplicate: EventEmitter<any> = new EventEmitter();
    @Output() onPublishPage: EventEmitter<any> = new EventEmitter();    
    @Output() onSaveAndPreview: EventEmitter<any> = new EventEmitter();
    @Output() onActivate: EventEmitter<any> = new EventEmitter();
    @Output() onCursusEdit: EventEmitter<any> = new EventEmitter();

    constructor(private router: Router, private fb: FormBuilder, public store: StoreService) {}

    ngOnInit() {}

    save() {
        // console.log("save...");
        if (!this.isLoading) {
            this.onSave.emit();
        }
    }

    delete() {
        // console.log("delete...");
        if (!this.isLoading) {
            this.onDelete.emit();
        }
    }

    duplicate() {
        // console.log("clone...");
        if (!this.isLoading) {
            this.onDuplicate.emit();
        }
    }

    saveAndPreview() {
        // console.log("view...");
        if (!this.isLoading) {
            this.onSaveAndPreview.emit();
        }
    }

    publishPage() {
        if (!this.isLoading) {
            this.onPublishPage.emit();
        }
    }

    
    cursus() {
        // console.log("view...");
        if (!this.isLoading) {
            this.onCursusEdit.emit();
        }
    }

    activate() {
        if (!this.isLoading) {
            this.onActivate.emit();
        }
    }
}



