    focusRangeChanged(e) {
        this.props.focusedInput = e;
        this.rcptDate.binding(this.props);
    }

    datesChanged(e) {
        this.props.focusedInput = null;
        this.props.startDate = e.startDate;
        this.props.endDate = e.endDate;
        this.startDate = e.startDate;
        this.endDate = e.endDate;

        // if (null != e.startDate) {
        this.dateForm.patchValue({ periodeDebut: e.startDate });
        // }

        if (
            null != e.startDate &&
            null != this.endDate &&
            this.startDate.isBefore(moment(this.endDate).add(-31, "days"))
        ) {
            const newDate = this.startDate.clone().add(31, "days");
            this.endDate = newDate;
            this.props.endDate = newDate;
            this.dateForm.patchValue({ periodeFin: newDate });
        } else {
            // if (null != e.endDate) {
            this.dateForm.patchValue({ periodeFin: e.endDate });
            // }
        }

        // if (null != e.startDate && null != e.endDate) {
        this.rcptDate.binding(this.props);
        // }
    }

    onCategorieSelected($event) {
        //console.log("selected categories :", $event);

        if ($event.items.length > 0) {
            this.categorieDropdownPlaceholder = $event.items[0]["title"];
            this.selectedCategorie = $event.items[0];
        } else {
            this.categorieDropdownPlaceholder = "Aucune sélection";
            this.selectedCategorie = null;
        }

        this.showCategorieDropdown = false;
    }

    onGroupeSelected($event) {
        //console.log("selected categories :", $event);

        if ($event.items.length > 0) {
            this.groupeDropdownPlaceholder = $event.items[0]["nom"];
            this.selectedGroupe = $event.items[0];
        } else {
            this.groupeDropdownPlaceholder = "Aucune sélection";
            this.selectedGroupe = null;
        }

        this.showGroupeDropdown = false;
    }

