    confirmExportCommande() {
        // console.log("this.dateForm.valid", this.dateForm.valid, this.dateForm);
        if (this.dateForm.valid) {
            this.commandeApi
                .apiCommandesMagCsvsPost(
                    {
                        start: this.props.startDate.format("YYYY-MM-DD"),
                        end: this.props.endDate.format("YYYY-MM-DD")
                    },
                    this.store.paiementId
                )
                .subscribe(t => {
                    let fields = [
                        "Num. Facture",
                        " Date",
                        "Devise",
                        "Tva Non Récuperable",
                        "Désignation",
                        "Total TTC",
                        "Total HT",
                        "Mode de Livraison",
                        "Livraison TTC",
                        "Livraison HT",
                        "Code de Réduction",
                        "Description réduction",
                        "Réduction TTC",
                        "Réduction HT",
                        "Options TTC",
                        "Options HT",
                        "Ref. Paiement",
                        "Ref. Commande",
                        "Genre",
                        "Prénom",
                        "Nom",
                        "Adresse",
                        "Adresse (suite)",
                        "Code postal",
                        "Ville",
                        "Pays",
                        "Email"
                    ];

                    const idxColumnPays = fields.findIndex(c => c === "Pays");

                    let processRow = function (row) {
                        let finalVal = "";

                        for (let j = 0; j < row.length; j++) {
                            let innerValue =
                                row[j] === null ? "" : row[j].toString();

                            if (j === idxColumnPays && row[j] !== "Pays") {
                                // exclus la premiere ligne du csv
                                innerValue = getCountryName(row[j]);
                            }
                            if (row[j] instanceof Date) {
                                innerValue = row[j].toLocaleString();
                            }
                            let result = innerValue.replace(/"/g, '""');
                            if (result.search(/("|,|\n)/g) >= 0)
                                result = '"' + result + '"';
                            if (j > 0) finalVal += ",";
                            finalVal += result;
                        }
                        return finalVal;
                    };

                    //console.log("return CSV", t);
                    //const data = [["name1", "city1", "some other info"], ["name2", "city2", "more info"]];
                    //console.log("CSV", Object.values(t));

                    let lineArray = [];
                    lineArray.push(processRow(fields));
                    t.forEach(function (infoArray, index) {
                        lineArray.push(processRow(Object.values(infoArray)));
                    });

                    //console.log(JSON.stringify(lineArray));
                    let csvContent = lineArray.join("\n");
                    let downloadLink = document.createElement("a");
                    let blob = new Blob(["\ufeff", csvContent]);
                    let url = URL.createObjectURL(blob);

                    downloadLink.href = url;
                    downloadLink.download =
                        "Commandes-du-" +
                        this.props.startDate.format("YYYY-MM-DD") +
                        "-au-" +
                        this.props.endDate.format("YYYY-MM-DD") +
                        ".csv";
                    document.body.appendChild(downloadLink);
                    downloadLink.click();
                    document.body.removeChild(downloadLink);
                    this.modalExportCommande.close();
                });
        }
    }

