    add() {
        if (this.domain.name == "user") {
            //console.log('Add User');
            this.modalUser.open();
        } else if (this.domain.name == "formation") {
            this.shopService.fetchNbProducts(() => {
                this.showLimitFormation = this.shopService.hasReachedMaxNbFormations();
                if (!this.showLimitFormation) {
                    this.router.navigate([this.path.addPath]);
                }
            });

        } else {
            /**
             *  on navigue vers la route
             * si le path est different des 3 paths ci dessous
             * OU que la limite n'est pas atteinte pour l'ajout de produit
             */
            this.shopService.fetchNbProducts(() => {
                this.showLimit = ["produit"].indexOf(this.path.listing) + 1 && this.shopService.hasReachedMaxNbProducts();
                if (!this.showLimit) {

                    this.router.navigate([this.path.addPath]);
                }
            });

        }
    }

    addUser() {
        if (this.userForm.valid) {
            //console.log('Add User', this.userForm.value);
            let userData = { ...this.userForm.value };
            userData["channels"] = [2];
            this.userForm.reset();

            this.userApi
                .apiMagasinsSlugUsersPost(userData, this.store.memberId)
                .subscribe(e => {
                    //console.log(e);
                    this.onReRender.emit();
                    this.modalUser.close();
                });
        } else {
            console.error("Add User", this.userForm.value);
        }
    }

