import { Component, OnInit, EventEmitter, Output, Input } from "@angular/core";
import { StoreService } from "../../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CategorieService } from "../../../../api-shop";

@Component({
    selector: "app-shop-categorie-form",
    templateUrl: "./categorie-form.component.html",
    styleUrls: ["./categorie-form.component.scss"]
})
export class CategorieFormComponent implements OnInit {
    myForm: FormGroup;
    @Output() added: EventEmitter<any> = new EventEmitter();
    @Output() edited: EventEmitter<any> = new EventEmitter();
    @Input() categorie;

    constructor(public store: StoreService, private fb: FormBuilder, private apiCategorie: CategorieService) {}

    ngOnInit() {
        // console.log("init categorie :", this.categorie);
        this.createForm();
    }

    displayFieldCss(form, field: string) {
        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    createForm() {
        let title = "";
        // let description = "";

        if (this.categorie.id) {
            title = this.categorie.title;
            // description = this.categorie.description;
        }

        this.myForm = this.fb.group({
            title: [title, [Validators.required, Validators.minLength(2), Validators.maxLength(150)]]
            // description: [description, Validators.required]
        });
    }

    async submitHandler() {
        // console.log("submit categorie");
        let formValue = this.myForm.value;
        let id = null;
        if (this.categorie.id) {
            id = this.categorie.id;
            this.apiCategorie
                .apiMagasinsSlugCategoriesCategoriePut({ title: formValue.title, lvl: 1, ordre: 0 }, this.store.shopId.toString(), id)
                .subscribe(e => {
                    this.edited.emit({ id: id, title: formValue.title });
                    this.myForm.reset();
                    this.createForm();
                });
        } else {
            this.apiCategorie
                .apiMagasinsSlugCategoriesPost({ title: formValue.title, lvl: 1, ordre: 0 }, this.store.shopId.toString())
                .subscribe(e => {
                    this.added.emit({ id: e.id, title: formValue.title });
                    this.myForm.reset();
                    this.createForm();
                });
        }
    }
}



