    byValue(item1, item2) {
        if (!item2) {
            return false;
        }
        return item1.id == item2.id;
    }

    dataChanged($event) {
        this.loadPluginForm($event.id, null);
    }

    paysChanged($event) {
        this.ModeDePaiementForm.patchValue({ pays: $event });
    }

    onSubmit() {
        if (this.ModeDePaiementForm.valid) {
            this.isLoading = true;
            //console.log("TRANSFORM BEFORE POST: ", this.ModeDePaiementForm.value);

            let optP = { ...this.ModeDePaiementForm.value };
            let optObj = { ...this.ModeDePaiementForm.value };

            delete optObj["plugin"];
            delete optObj["minAchat"];

            if (typeof optObj["pays"] != "undefined") {
                optObj["pays"] = optObj["pays"].codeIso;
            }

            let obj = {
                minimumAchat: optP.minAchat,
                logo: this.imagePath,
                infosJson: JSON.stringify(optObj),
                active: true,
                moyenPaiementOfficiel: optP.plugin
            };

            if (this.Id) {
                this.moyenApi
                    .apiMagasinsMagMoyensMpbPut(
                        obj,
                        this.store.paiementId.toString(),
                        this.Id
                    )
                    .subscribe(e => {
                        // console.log("MPay:", e);
                        this.isLoading = false;
                        this.openInfoPro();
                    });
            } else {
                this.moyenApi
                    .apiMagasinsMagMoyensPost(
                        obj,
                        this.store.paiementId.toString()
                    )
                    .subscribe(e => {
                        // console.log("MPay:", e);
                        this.Id = e.id;
                        this.router.navigate([
                            "shop/modedepaiement/modedepaiement/" + e.id
                        ]);
                        this.isLoading = false;
                        this.openInfoPro();
                    });
            }
        } else {
            //   console.log('invalid form', this.ModeDePaiementForm.valid ) ;
            this.getFormValidationErrors(this.ModeDePaiementForm);
            scrollTo(["we-is-invalid", "dropdown-error"]);
            this.isLoading = false;
        }
    }

    getFormValidationErrors(f) {
        Object.keys(f.controls).forEach(key => {
            const controlErrors: ValidationErrors = f.get(key).errors;
            if (controlErrors != null) {
                Object.keys(controlErrors).forEach(keyError => {
                    console.log(
                        "Key control: " +
                        key +
                        ", keyError: " +
                        keyError +
                        ", err value: ",
                        controlErrors[keyError]
                    );
                });
            }
        });
    }






