  toggleStock() {
    this.produit["illimited"] = !this.produit["illimited"];
    this.produitForm.patchValue({ illimited: this.produit["illimited"] });
    this.produitForm.patchValue({ quantite: 0, stockCritique: 0 });
  }

  togglePromo() {
    //console.log('toggle Promo');
    if (this.produit["prixSpecifics"].length > 0) {
      this.produit["prixSpecifics"] = [];
    } else {
      this.produit["prixSpecifics"] = [
        {
          prixHt: this.produit["prixHt"],
          promotion: 5,
          typePromotion: true
        }
      ];
    }
    this.finalPrice(null);
  }

  changePromotionType(type) {
    if (type) {
      this.produit["prixSpecifics"][0].typePromotion = true;
    } else {
      this.produit["prixSpecifics"][0].typePromotion = false;
    }

    if (!this.produit["prixSpecifics"][0].typePromotion) {
      if (this.produit["prixSpecifics"][0].promotion > 100) {
        this.produit["prixSpecifics"][0].promotion = 100;
      }
    }

    this.finalPrice(null);
  }

  calculatePromotion(e) {
    this.produit["prixHt"] = e.target.value;
    this.finalPrice(e.target.value);
    this.produitForm.patchValue({ prixHt: this.produit["prixHt"] });
  }

  calculateHt(e) {
    let txTva = 1;
    if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
      txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
    }

    let px = e.target.value.toString().replace(",", ".");
    this.originalPrice = e.target.value;
    this.produit["prixHt"] = (px / txTva)
      .toFixed(6)
      .toString()
      .replace(".", ",");
    this.produitForm.patchValue({ prixHt: this.produit["prixHt"] });
    // console.log('prixHt utilisé :',  ( px  /  txTva ) );
    // console.log('txTva utilisé :', txTva);
    this.finalPrice(null);
  }

  finalPrice(price) {
    if (this.produit["prixSpecifics"].length > 0) {
      let txTva = 1;
      if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
        txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
      }
      let prix;
      if (!price) {
        prix = this.produit["prixHt"].toString().replace(",", ".");
      } else {
        prix = price.toString().replace(",", ".");
      }
      //    console.log('prixHt utilisé :', prix);
      //    console.log('txTva utilisé :', txTva);
      this.originalPrice = (parseFloat(prix) * txTva)
        .toFixed(2)
        .toString()
        .replace(".", ",");
      //    console.log('px origine:', this.originalPrice , (parseFloat(prix) * txTva).toFixed(2));

      if (!this.produit["prixSpecifics"][0].typePromotion) {
        this.finalPriceValue = (
          parseFloat(prix) *
          txTva *
          (1 -
            parseFloat(
              this.produit[
                "prixSpecifics"
              ][0].promotion.toString()
            ) /
            100)
        )
          .toFixed(2)
          .toString()
          .replace(".", ",");
      } else {
        this.finalPriceValue = (
          txTva * parseFloat(prix) -
          parseFloat(this.produit["prixSpecifics"][0].promotion)
        )
          .toFixed(2)
          .toString()
          .replace(".", ",");
      }

      if (parseFloat(this.finalPriceValue) < 0) {
        this.finalPriceValue = "0";
      }
    }

    if (typeof this.produitForm != "undefined") {
      const control = <FormArray>(
        this.produitForm.controls["declinaisons"]["controls"]
      );
      if (control.length > 0) {
        let txTva = 1;
        if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
          txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
        }

        for (let dec = 0; dec < control.length; dec++) {
          let variation =
            parseFloat(
              control[dec]
                .get("variationHt")
                .value.toString()
                .replace(",", ".")
            ) * txTva;
          let $event = { target: { value: variation } };
          let item = control[dec];
          item.patchValue(this.modVariantTtc($event, null));
        }
        //
      }
    }
  }

  changePromotionValue($event) {
    let value = $event.target.value;
    console.log("TODO --> VALIDATION QUE LE MONTANT PROMO EST VALID");
    if (!this.produit["prixSpecifics"][0].typePromotion) {
      if (value > 100) {
        value = 100;
      }
    }
    this.produit["prixSpecifics"][0].promotion = value
      .toString()
      .replace(",", ".");
    this.finalPrice(null);
  }

  promotionType() {
    if (!this.produit["prixSpecifics"][0].typePromotion) {
      return "%";
    } else {
      return this.devise;
    }
  }

  onCheckall() {
    this.cd.detectChanges();
    this.selectedDeclisState = !this.selectedDeclisState;
    this.selectedDeclis = 0;
    const controlArray = this.produitForm.controls["declinaisons"][
      "controls"
    ];
    let state = this.selectedDeclisState ? "checked" : null;
    for (let control of controlArray) {
      control.patchValue({ selected: state });
      //console.log(control.value.selected);
      if (state) {
        this.selectedDeclis = this.selectedDeclis + 1;
      }
    }
  }

  applyBatch() {
    if (this.selectedDeclis > 0 && this.myFormBatch.valid) {
      const controlArray = this.produitForm.controls["declinaisons"][
        "controls"
      ];
      let obj = { ...this.myFormBatch.value };
      let price = {};

      Object.keys(obj).forEach(key => {
        if (obj[key] == null || obj[key] == "") delete obj[key];
        if (key == "variationTtc") {
          if (typeof obj[key] !== "undefined") {
            price = {
              ...this.modVariantTtc(
                { target: { value: obj[key] } },
                null
              )
            };
          }
        }
      });

      const newItem = Object.assign({}, obj, price);
      for (let control of controlArray) {
        if (control.value.selected) {
          //console.log('recalcul de la promo', newItem);
          control.patchValue({ ...newItem });
        }
      }

      this.myFormBatch.reset();
    }
  }

  isSelected(variant) {
    const control = <FormArray>this.produitForm.controls["declinaisons"];
    if (!variant.value.selected) {
      return null;
    } else {
      return "checked";
    }
  }

  onSelectedItem(variant) {
    variant.patchValue({ selected: !variant.value.selected });
    if (variant.value.selected) {
      this.selectedDeclis = this.selectedDeclis + 1;
    } else {
      this.selectedDeclis = this.selectedDeclis - 1;
    }
  }

