  loadProduct(e) {
    // console.log("load product", e);
    if (e.produitPersonnalises && e.produitPersonnalises.length > 0) {
      this.selectedPerso = {
        id: e.produitPersonnalises[0].id,
        nom: e.produitPersonnalises[0].nom,
        fields: JSON.parse(e.produitPersonnalises[0].infosJson)
      };
    }

    this.isProduitLoaded = true;
    this.produit = { ...e };
    this.declinaisons = this.produit["declinaisons"];
    if (this.produit['downloadLink']) {
      this.produit['downloadLink'] = JSON.parse(this.produit['downloadLink']);
    }


    if (!this.produit.imageUrl) {
      this.produit.imageUrl = this.appService.PLACEHOLDER_IMG;
    }

    if (this.isAE) {
      // console.log("is A.E");
      this.originalPrice = this.produit.prixHt;
      // this.produit["groupeRegleTaxe"] = {
      //     libelle: "Groupe 20%",
      //     active: true,
      //     tempTaux: 20
      // };
    }

    if (this.declinaisons.length > 0) {
      for (let d = 0; d < this.declinaisons[0].attributValeurs.length; d++) {
        let cloneAttr = {
          ...this.attributs.filter(att => att.id == this.declinaisons[0].attributValeurs[d].pId)[0]
        };
        this.attributsFiltered.push(cloneAttr);
      }
    }

    if (this.produit["prixSpecifics"].length > 0) {
      this.finalPrice(null);
    } else {
      let txTva = 1;
      if (typeof this.produit["groupeRegleTaxe"] != "undefined") {
        txTva = 1 + this.produit["groupeRegleTaxe"].tempTaux / 100;
      }
      let prix = this.produit["prixHt"].toString().replace(",", ".");

      this.originalPrice = (parseFloat(prix) * txTva)
        .toFixed(2)
        .toString()
        .replace(".", ",");

      // console.log("originalPrice :", prix, txTva);
    }

    this.createForm();

    // console.log(this);

    this.initFirstSlideGallery();
  }

  initFirstSlideGallery() {
    try {
      const srcs = JSON.parse(this.produitForm.value.imageUrl);
      this.placeholderImg = srcs["1920"];
    } catch (e) {
      this.placeholderImg = this.produitForm.value.imageUrl;
    }
  }

