  toggleDropdown(name) {
    if (!this[name]) {
      let module = this.modules.filter(module => module.dropdown == name);
      //console.log("request data pour maj le storeItem - show loader", name , this[name], module[0]);
      this.populateStore(module[0]);
    }

    this[name] = !this[name];
  }

  displayDropdownCss(field) {
    return {
      "dropdown-error": field.invalid
    };
  }

  dropdownPlaceholderText(moduleName, items) {
    let toggle = moduleName["toggle"];
    let field = moduleName["field"];
    let field2 = moduleName["field2"];

    if (items.length == 1) {
      if (typeof field2 != "undefined") {
        this[toggle] =
          '<img src="' +
          items[0][field2] +
          '" height="25px" /> ' +
          items[0][field];
      } else {
        if (typeof items[0] == "undefined") {
          //console.log(items,moduleName, 'not defined');
        } else {
          this[toggle] = items[0][field];
        }
      }
    } else {
      if (toggle == "messagestockcritiqueDropdownPlaceholder") {
        this[toggle] = "Stock critique";
      } else if (toggle == "messagestockDropdownPlaceholder") {
        this[toggle] = "En stock";
      } else if (toggle == "messagehorsstockDropdownPlaceholder") {
        this[toggle] = "Hors stock";
      } else {
        this[toggle] = items.length + " selectionnés";
      }
    }
  }

  clearFormArray = (formArray: FormArray) => {
    for (let i = formArray.length - 1; i >= 0; i--) {
      formArray.removeAt(i);
    }
  };

  fillDropdownData(formArray, data, required) {
    this.clearFormArray(formArray);
    if (data.length == 0) {
      //formArray.setValue([{id:""}]);
    } else {
      data.forEach(element => {
        if (required) {
          formArray.push(
            this.fb.group(
              { id: [element, Validators.required] },
              Validators.required
            )
          );
        } else {
          formArray.push(
            this.fb.group({ id: [element, Validators.required] })
          );
        }
      });
    }
  }

