import {
    Component,
    OnInit,
    AfterViewInit,
    ViewChild,
    ElementRef,
    ChangeDetectorRef
} from "@angular/core";
import { ScriptService } from "../../../shared/services/script.service";
import { DateRangePicker } from "react-dates";
import { ReactCptComponent } from "../../../shared/components/react-cpt/react-cpt.component";
import { StatsGraph } from "../../../api-paiement/model/statsGraph";
import { StatsService as StatsServicePaiement } from "../../../api-paiement/api/stats.service";
import { StoreService } from "../../../shared/services/store.service";
import { StatsService as StatsServiceShop } from "../../../api-shop";
import { PermissionService } from "../../../shared/services/permission.service";

interface ListDashboard {
    id: string;
    loadedCounter: boolean;
    loadedTable: boolean;
    label: string;
    showCounter: boolean;
    counter?: number;
    isOpen: boolean;
    table: { head: string[]; body: any[] };
    api: "paiement" | "shop";
    getStats: string;
    pendingRequest: boolean;
}

interface ChartDashboard {
    id: number;
    label: string;
    gradient_color_top: string;
    gradient_color_bot: string;
    complete_name: string;
    extension_name: string;
    border_color: string;
    getStats: string;
}
interface BtnDateChart {
    id: number;
    label: string;
    custom?: boolean;
    startDate: any;
    endDate: any;
}
declare var Chart: any;
declare var moment: any;
declare var $: any;
@Component({
    selector: "shop-dashboard-stats",
    templateUrl: "./dashboard-stats.component.html",
    styleUrls: ["./dashboard-stats.component.scss"]
})
export class DashboardStatsComponent implements OnInit, AfterViewInit {
    @ViewChild("myChart") myChart: ElementRef;
    @ViewChild(ReactCptComponent) rcptDate: ReactCptComponent;

    lineChartType = "line";
    dateFormat_normal = "DD/MM/YYYY";
    dateFormat_sql = "YYYY-MM-DD";
    today = moment();
    date7jours = moment().subtract(6, "days");
    date30jours = moment().subtract(29, "days");
    paramsTempGraph = {
        active_mode: "btn7jours",
        dateDebut: this.date7jours,
        dateFin: null
    };

    btnDates: BtnDateChart[] = [
        {
            id: 1,
            label: "7 jours",
            startDate: this.date7jours,
            endDate: this.today
        },
        {
            id: 2,
            label: "30 jours",
            startDate: this.date30jours,
            endDate: this.today
        },
        {
            id: 3,
            label: "",
            custom: true,
            startDate: this.date7jours,
            endDate: this.today
        }
    ];
    charts: ChartDashboard[] = [
        {
            id: 1,
            label: "Ventes",
            complete_name: "Chiffre d'affaire",
            extension_name: " {{devise}}",
            border_color: "151,233,105,1",
            gradient_color_top: "151,233,105,1",
            gradient_color_bot: "134,223,233,1",
            getStats: "apiMagasinsSlugStatsChiffreAffairesGet"
        },
        {
            id: 2,
            label: "Commandes validées",
            complete_name: "Commandes validées",
            extension_name: "",
            border_color: "255,102,102,1",
            gradient_color_top: "255,102,102,1",
            gradient_color_bot: "255,199,70,1",
            getStats: "apiMagasinsSlugStatsNbCommandesValideesGet"
        },
        // {
        //     id: 3,
        //     label: "Articles vendus",
        //     complete_name: "Articles vendus",
        //     extension_name: "",
        //     border_color: "80,227,194,1",
        //     gradient_color_top: "80,227,194,1",
        //     gradient_color_bot: "134,173,233,1"
        // },
        {
            id: 4,
            label: "Panier moyen",
            complete_name: "Panier moyen",
            extension_name: " {{devise}}",
            border_color: "155,132,236,1",
            gradient_color_top: "155,132,236,1",
            gradient_color_bot: "216,136,232,1",
            getStats: "apiMagasinsSlugStatsPanierMoyenGet"
        }
    ];

    chartJs = null;
    chartSelected: ChartDashboard = this.charts[0];
    btnDateSelected: BtnDateChart = this.btnDates[0];
    component = DateRangePicker;
    focusedInput = null;
    props = {
        startDatePlaceholderText: "Date de début",
        endDatePlaceholderText: "Date de fin",
        startDate: this.date7jours,
        startDateId: "your_unique_start_date_id",
        endDate: this.today,
        endDateId: "your_unique_end_date_id",
        onDatesChange: ({ startDate, endDate }) =>
            this.datesChanged({ startDate, endDate }), // PropTypes.func.isRequired,
        focusedInput: this.focusedInput,
        onFocusChange: f => this.focusRangeChanged(f),
        displayFormat: "DD/MM/YYYY",
        isOutsideRange: date => this.handleDateRange(date),
        hideKeyboardShortcutsPanel: true
    };

    lists: ListDashboard[] = [
        {
            id: "list-dashboard-cpnt",
            label: "Commandes payées non traitées",
            showCounter: true,
            counter: 0,
            loadedCounter: false,
            loadedTable: false,
            isOpen: false,
            table: {
                head: [
                    "Date",
                    "Email client",
                    "Référence",
                    "Mode Paiement",
                    "Total",
                    ""
                ],
                body: []
            },
            api: "paiement",
            getStats: "apiMagasinsSlugStatsCommandesPayeesNonTraiteesGet",
            pendingRequest: false
        },
        {
            id: "list-dashboard-ceadp",
            label: "Commandes en attente de paiement",
            showCounter: true,
            counter: 0,
            loadedCounter: false,
            loadedTable: false,
            isOpen: false,
            table: {
                head: [
                    "Date",
                    "Email client",
                    "Référence",
                    "Mode paiement",
                    "Total",
                    "Statut du paiement",
                    ""
                ],
                body: []
            },
            api: "paiement",
            getStats: "apiMagasinsSlugStatsCommandesEnAttentePaiementGet",
            pendingRequest: false
        },
        {
            id: "list-dashboard-prds",
            label: "Produits en rupture de stock",
            showCounter: true,
            counter: 0,
            loadedCounter: false,
            loadedTable: false,
            isOpen: false,
            table: {
                head: ["Référence", "Nom", "Ajouter stock", ""],
                body: []
            },
            api: "shop",
            getStats: "apiMagasinsSlugProductHorsStockGet",
            pendingRequest: false
        },
        {
            id: "list-dashboard-psc",
            label: "Produits en stock critique",
            showCounter: true,
            counter: 0,
            loadedCounter: false,
            loadedTable: false,
            isOpen: false,
            table: {
                head: [
                    "Référence",
                    "Nom",
                    "Quantité",
                    "Stock critique",
                    "Ajouter stock",
                    ""
                ],
                body: []
            },
            api: "shop",
            getStats: "apiMagasinsSlugProductStockCritiqueGet",
            pendingRequest: false
        }
        // {
        //     id: "list-dashboard-avis",
        //     label: "Avis à valider",
        //     showCounter: true,
        //     counter: 0,
        //     loadedCounter: false,
        //     loadedTable: false,
        //     isOpen: false,
        //     table: {
        //         head: ["Note", "Titre", "Avis", "Pseudo", "Date", "Produit concerné"],
        //         body: [
        //             {
        //                 aze: "ttyeye",
        //                 hhh: "dfgdfgdfg",
        //                 dfg: "xcvxvxcv",
        //                 gg: "vbbnbvn",
        //                 xcv: "khkhjkhk",
        //                 rz: "pipiopip"
        //             }
        //         ]
        //     }
        // }
    ];

    statsGraph: StatsGraph[] = [];
    firstLoadGraph = true;
    pendingRequest = false;
    devise = { sign: "€" };

    constructor(
        private scriptService: ScriptService,
        private statsPaiementApi: StatsServicePaiement,
        private statsShopApi: StatsServiceShop,
        private store: StoreService,
        private cd: ChangeDetectorRef,
        public permService: PermissionService
    ) {}

    ngOnInit() {
        this.scriptService.addScript({
            id: "chartjsScript",
            name: "chartjsScript",
            src:
                "https://cdnjs.cloudflare.com/ajax/libs/Chart.js/2.8.0/Chart.min.js",
            type: "js"
        });
        this.scriptService.addScript({
            id: "chartjsCss",
            name: "chartjsCss",
            src:
                "https://cdnjs.cloudflare.com/ajax/libs/Chart.js/2.8.0/Chart.min.css",
            type: "css"
        });

        this.getAllCounterList();
    }

    ngAfterViewInit(): void {
        this.scriptService.loader("chartjsScript", "chartjsCss").then(() => {
            const cfgShop = this.store.designModules.config["shop"];
            if (cfgShop && cfgShop.commun && cfgShop.commun.devise) {
                this.devise = cfgShop.commun.devise;
                for (let i = 0; i < this.charts.length; i++) {
                    this.charts[i].extension_name = this.charts[
                        i
                    ].extension_name.replace("{{devise}}", this.devise.sign);
                }
            }
            this.showChart(this.chartSelected);
            this.handleIconOpenList();
            // $("#collapse").collapse();
            // this.initChartJs2();
        });
    }

    getAllCounterList() {
        this.lists.forEach(list => {
            switch (list.api) {
                case "paiement":
                    // Si on as pas de paiementId, ca veut dire que l'activation n'a pas retourer uid paiement
                    if (this.store.paiementId) {
                        this.statsPaiementApi[list.getStats](
                            this.store.paiementId,
                            "count",
                            null
                        ).subscribe(data => {
                            list.counter = data;
                            list.loadedCounter = true;
                        });
                    } else {
                        // Forcement a 0 ducoup..
                        list.counter = 0;
                        list.loadedCounter = true;
                    }

                    break;
                case "shop":
                    this.statsShopApi[list.getStats](
                        this.store.shopId,
                        "count",
                        null
                    ).subscribe(data => {
                        list.counter = data;
                        list.loadedCounter = true;
                    });
                    break;
            }
        });
    }

    handleIconOpenList() {
        // this.cd.detectChanges(); car event via jquery/bootstrap
        $("#lists_dashboard").on("show.bs.collapse", e => {
            const list = this.lists.find(l => l.id === e.target.id);
            list.isOpen = true;
            this.loadDataTable(list);

            this.cd.detectChanges();
        });
        $("#lists_dashboard").on("hidden.bs.collapse", e => {
            const list = this.lists.find(l => l.id === e.target.id);
            list.isOpen = false;
            this.cd.detectChanges();
        });
    }

    datesChanged(e) {
        this.props.focusedInput = null;
        this.props.startDate = e.startDate;
        this.props.endDate = e.endDate;

        if (null != e.startDate && null != e.endDate) {
            this.rcptDate.binding(this.props);
            this.showChart(this.chartSelected);
        }
    }

    handleDateRange(date) {
        return (
            date.isAfter(moment(), "day") ||
            Math.abs(date.diff(this.props.endDate, "year")) >= 2
        );
    }

    focusRangeChanged(f) {
        this.btnDateSelected = this.btnDates.find(btn => btn.custom);
        this.props.focusedInput = f;
        this.rcptDate.binding(this.props);
    }

    showChart(chart: ChartDashboard) {
        if (this.pendingRequest) {
            return;
        }
        this.chartSelected = chart;
        const {
            gradient_color_top,
            gradient_color_bot,
            complete_name,
            extension_name,
            border_color
        } = chart;
        const el = this.myChart.nativeElement;
        const ctx = el.getContext("2d");
        const gradient = ctx.createLinearGradient(0, 0, 0, 400);

        gradient.addColorStop(0, "rgba(" + gradient_color_top + ")");
        gradient.addColorStop(1, "rgba(" + gradient_color_bot + ")");

        const options = {
            responsive: true,
            maintainAspectRatio: false,
            datasetStrokeWidth: 3,
            pointDotStrokeWidth: 4,
            tooltipFillColor: "rgba(0,0,0,0.8)",
            tooltipFontStyle: "bold",
            scales: {
                xAxes: [
                    {
                        gridLines: {
                            display: false
                        },
                        ticks: {
                            maxTicksLimit: 30
                        }
                    }
                ],
                yAxes: [
                    {
                        gridLines: {
                            display: false
                        },
                        ticks: {
                            beginAtZero: true,
                            callback: function(value, index, values) {
                                if (Math.floor(value) === value) {
                                    return value.toLocaleString();
                                }
                            }
                        }
                    }
                ]
            },
            legend: {
                display: false
            },
            tooltips: {
                callbacks: {
                    label: function(tooltipItem, data) {
                        //double parseFloat car toFixed casse la mise en forme de toLocaleString()
                        return (
                            complete_name +
                            " : " +
                            parseFloat(
                                parseFloat(
                                    data.datasets[0].data[tooltipItem.index]
                                ).toFixed(2)
                            ).toLocaleString() +
                            extension_name
                        );
                    }
                }
            }
        };
        this.getData(complete_name, gradient, border_color, data => {
            if (!this.chartJs) {
                this.chartJs = new Chart(ctx, {
                    type: this.lineChartType,
                    data,
                    options
                });
            } else {
                this.chartJs.data = data;
                this.chartJs.options = options;
                this.chartJs.update();
            }
        });
    }

    loadData(btn: BtnDateChart) {
        if (!btn.custom) {
            if (this.btnDateSelected.id === btn.id) {
                return;
            }
            this.btnDateSelected = btn;
            this.changeDateCustom(btn);
        } else {
            this.btnDateSelected = btn;
        }
    }

    changeDateCustom(btn: BtnDateChart) {
        this.props.startDate = btn.startDate;
        this.props.endDate = moment();
        this.rcptDate.binding();
        this.showChart(this.chartSelected);
    }

    getData(complete_name, gradient, border_color, onSuccess: Function) {
        const data = {
            labels: [],
            datasets: [
                {
                    label: complete_name,
                    fill: true,
                    backgroundColor: gradient, // Put the gradient here as a fill color
                    data: [],
                    borderColor: "rgba(" + border_color + ")",
                    pointHitRadius: 40,
                    spanGaps: true
                }
            ]
        };

        // request api here
        this.requestData(result => {
            let tab_date = this.GenerateDays(
                this.props.startDate,
                this.props.endDate
            );

            for (let i in result) {
                if (result[i]) {
                    tab_date[
                        moment(result[i].jour).format(this.dateFormat_normal)
                    ] = result[i].result != null ? result[i].result : 0;
                }
            }
            const dataChart = this.getDataChart(tab_date);

            // const result = [labels, valueLabels];
            data.labels = dataChart[0];
            data.datasets[0].data = dataChart[1];
            onSuccess(data);
        });
    }

    requestData(onSuccess: Function) {
        if (!this.store.paiementId) {
            this.pendingRequest = false;
            return;
        }
        this.pendingRequest = true;
        this.statsPaiementApi[this.chartSelected.getStats](
            this.store.paiementId,
            this.props.startDate.format(this.dateFormat_sql),
            this.props.endDate.format(this.dateFormat_sql)
        ).subscribe(statsGraph => {
            onSuccess(statsGraph);
            this.pendingRequest = false;
            this.firstLoadGraph = false;
        });
    }
    /**
     * Genere les dates entre un interval de dates
     * @param {string} dateDebut
     * @param {string} dateFin
     * @returns {array} de Dates
     */
    GenerateDays(dateDebut, dateFin) {
        let dates = [];
        if (dateFin == null) {
            dateFin = moment().format(this.dateFormat_sql);
        }

        let currDate = moment(dateDebut, this.dateFormat_sql)
            .subtract(1, "days")
            .clone()
            .startOf("day");
        let lastDate = moment(dateFin, this.dateFormat_sql)
            .clone()
            .startOf("day");
        while (currDate.add(1, "days").diff(lastDate) <= 0) {
            dates[currDate.clone().format(this.dateFormat_normal)] = 0;
        }
        return dates;
    }

    /**
     * Insère les data recuperer en ajax dans le tableau a fournir pour le chartjs
     * @param {array} tab
     */
    getDataChart(tab) {
        let tab_labels = [];
        let tab_data = [];
        let index = 0;
        for (let i in tab) {
            if (typeof tab[i] !== undefined) {
                tab_labels[index] = i;
                tab_data[index] = tab[i];
                index++;
            }
        }
        return new Array(tab_labels, tab_data);
    }

    loadDataTable(list: ListDashboard) {
        if (list.loadedTable || list.pendingRequest) {
            return;
        }
        list.pendingRequest = true;
        switch (list.api) {
            case "paiement":
                this.statsPaiementApi[list.getStats](
                    this.store.paiementId,
                    "list",
                    20
                ).subscribe(data => {
                    list.table.body = data;
                    list.loadedTable = true;
                    list.pendingRequest = false;
                    this.cd.detectChanges(); // needed car appeler d'un event jquery/bootstrap (collapse)
                });
                break;
            case "shop":
                this.statsShopApi[list.getStats](
                    this.store.shopId,
                    "list",
                    20
                ).subscribe(data => {
                    list.table.body = data;
                    list.loadedTable = true;
                    list.pendingRequest = false;
                    this.cd.detectChanges(); // needed car appeler d'un event jquery/bootstrap (collapse)
                });
                break;
        }
    }

    decrementCounter(list: ListDashboard) {
        list.counter = list.counter - 1;
    }
}



